<template>
  <v-container id="pdfpreview" class="container--fluid">
    <div id="pdfpreviewcontent" class="mx-3 my-3 pb-4" style="border: 1px solid grey">
        <div class="pdfheader mx-4" style="display: flex; justify-content: space-between; padding-bottom: 10px; border-bottom: 1px solid grey;">
            <span style="display: inline-block;" class="mt-2">
                <v-img src="@/assets/one.png" max-height="120" max-width="120" class="mb-6" style="display: inline-block"/> 
                <ul style="list-style-type:none; padding-left: 4px; display: inline-block" class="pt-4">
                  <li style="font-size:2em; font-weight: 500;"> {{$store.getters.getClientName}} </li>
                  <li style="font-size:0.8em;  padding-left: 4px;">  {{$store.getters.getClientAddress.addressline}} </li>
                  <li style="font-size:0.8em;  padding-left: 4px;"> {{$store.getters.getClientAddress.postcode + ', '+ $store.getters.getClientAddress.city +', '+ $store.getters.getClientAddress.country}} </li>

                  <li class="mt-2 body-2"> Phone: {{$store.getters.getClientContact.phone}}  </li>
                  <li class="body-2"> Email: {{$store.getters.getClientContact.email}}  </li>
                  <li class="body-2"> GSTIN: <b>{{$store.getters.getClientTaxNumber.taxnumber}}</b></li>
                </ul>
            </span>

            <span style="display: flex; flex-direction: column;align-items: flex-end;" class="mr-4">
                <span class="display-3 mt-4"> Delivery Challan </span>
                <span style="display: flex; justify-content: space-between; width: 95%;">
                    <ul style="list-style-type:none; padding:0" class="mt-6">
                        <li class="subtitle-1"> Issued On: <span class="title"> {{delvdata.headerdetails.challandate}} </span> </li>
                        <li class="subtitle-1"> Challan #: <span class="title"> {{delvdata.delvchalnnum}} </span> </li>
                    </ul>
                    <v-img src="@/assets/qr.jpg" max-width="100px" max-height="100px" />
                </span>
            </span>
        </div>

        <div class="pdfheader mx-6 mt-3" style="display: flex; justify-content: space-between;">
                <v-simple-table>
                    <tbody>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Consignee </td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.headerdetails.partnername}} </td>
                        </tr>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Ship To </td>
                            <td style="border:0px" class="subtitle-2 font-weight-regular">
                                <ul style="list-style-type:none; padding:0">
                                    <li>{{delvdata.headerdetails.address.addressline}} </li>
                                    <li>{{delvdata.headerdetails.address.postcode + ', '+ delvdata.headerdetails.address.city }} </li>
                                    <li>{{delvdata.headerdetails.address.state + ', '+ delvdata.headerdetails.address.country}} </li>
                                    
                                    <li class="mt-1 body-2">Phone: {{delvdata.headerdetails.contactdetails.phone}} [ {{ delvdata.headerdetails.contactdetails.name}} ]</li>
                                    <li class="mt-1 body-2">Email: {{delvdata.headerdetails.contactdetails.email}} </li>
                                </ul>
                            </td>
                        </tr> 
                        <tr>
                            <td style="border:0px" class="subtitle-2"> GSTIN </td>
                            <td style="border:0px" class="body-2 font-weight-medium">{{delvdata.headerdetails.partnertaxnum}} </td>
                        </tr>
                    </tbody>
                </v-simple-table>
                <v-simple-table class="mt-5 mr-12">
                    <tbody>
                        <tr>
                            <td style="border:1px solid grey; border-radius: 5px; background-color: #D9D9D9; text-align: center" class="title" colspan="3"> ** Copy </td>
                        </tr>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Challan Type </td>
                            <td style="border:0px;">:</td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.headerdetails.challantype}} </td>
                        </tr>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Ref. type </td>
                            <td style="border:0px;">:</td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.headerdetails.refdoctype}} </td>
                        </tr> 
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Ref. ID </td>
                            <td style="border:0px;">:</td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.headerdetails.refdocid}} </td>
                        </tr> 
                    </tbody>
                </v-simple-table>
        </div>

        <v-simple-table class="mx-3 mt-5" style="border-radius: 1px; margin-bottom: 2px; border-top: 1.2px solid #D9D9D9; border-bottom: 1.2px solid #D9D9D9">
        <thead >
          <tr>
            <th class="text-center">#</th>
            <th class="text-left" >Product</th>
            <th class="text-center" >HSN/SAC</th>
            <th class="text-center">Quantity</th>
            <th class="text-center">Unit</th>
            <th class="text-center">Rate ({{currency}})</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in delvdata.itemdetails" :key="index" >
            <td class="text-center" >{{index + 1}}</td>
            <td class="text-left" style="border-left: 1px solid #dddddd">
              <v-list-item two-line>
                <v-list-item-content>
                    <v-list-item-title class="font-weight-bold">{{item.product.name}} </v-list-item-title>
                    <label style="max-width: 350px; color: grey; font-size: 1em">{{item.product.desc}}</label>
                </v-list-item-content>
              </v-list-item>
            </td>
            <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.hsncode }}</td>
            <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.qty }}</td>
            <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.product.unit }}</td>
            <td class="text-center" style="border-left: 1px solid #dddddd; max-width: 100px; padding: 0">{{item.product.rate }}</td>
          </tr>
        </tbody>
      </v-simple-table>
    
      <div class="pdfheader mx-6 mt-3" style="display: flex; justify-content: space-between;">
        <ul style="list-style-type:none; padding:0">
            <li class="subtitle-2"><u> Comments </u></li>
            <li class="body-2 pl-3"> <div style="max-width: 800px; word-break: break-all;"> {{delvdata.comments}} </div></li>
        </ul>
        <v-simple-table class="mt-3 mr-12">
                    <tbody>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Shipment Inv. </td>
                            <td style="border:0px;">:</td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.shipmentdetails.shipmentinvoice}} </td>
                        </tr>
                        <tr>
                            <td style="border:0px" class="subtitle-2"> Vehicle Number: </td>
                            <td style="border:0px;">:</td>
                            <td style="border:0px" class="subtitle-1 font-weight-medium">{{delvdata.shipmentdetails.vehiclenumber}} </td>
                        </tr> 
                    </tbody>
                </v-simple-table>
      </div>
    <v-divider  class="my-6"/>

    <div style="display: flex; justify-content: center" class="headline pb-4"> <span style="border-bottom: 1px solid grey; padding: 0px 20px;"> Goods Received Receipt </span> </div>

    <div style="display: flex; justify-content: center" class="subtitle-1 mt-12 mb-12">
      <div class="mx-2" style="display: flex"> Received by: <div class="mx-2" style="min-width: 250px; border-bottom: 1px dashed grey"> </div></div>
      <div class="mx-2" style="display: flex"> Received Date: <div class="mx-2" style="min-width: 250px; border-bottom: 1px dashed grey"> </div></div>
      <div class="mx-2" style="display: flex"> Signature: <div class="mx-2" style="min-width: 250px; border-bottom: 1px dashed grey"> </div></div>
    </div>

    </div>
    <div style="display: flex; justify-content: flex-end" class="mr-8">
        <v-btn color ="info" class="mx-2"> Close </v-btn>
        <v-btn color ="info" class="mx-2"> Create Invoice </v-btn>
        <v-btn color ="info" class="mx-2"> Print Accounts Copy </v-btn>
        <v-btn color ="info" class="mx-2"> Print Sales Copy </v-btn>
        <v-btn color ="info" class="mx-2"> Print Customer Copy </v-btn>
    </div>
  </v-container>
  
</template>

<script>
//import * as html2pdf from 'html2pdf.js';

  export default {
    computed: {
        currency(){
            return this.$store.getters.getCurrencySymbol;
        }
    },
    data : () => ({
        delvdata:{
    "delvchalnnum" : "DC-0412321",
    "comments" : "Please check the Goods and provide the Goods Approval Receipt",
    "headerdetails": {
      "partnername": "G-tech Global Solutions",
      "partnertaxnum": "AGSJY7-HJAH2-GHSYT-21SGD",
      "outstandingBalance": null,
      "contactdetails": {
        "id": 1,
        "name": "Girish",
        "phone": "+49-16134456782",
        "email": "girish.12345@gmail.com"
      },
      "address": {
        "addressline": "Leopardstrase 43",
        "postcode": "90643",
        "city": "Munich",
        "state": "Bavaria",
        "country": "Germany"
      },
      "challantype": "Job Work",
      "challandate": "2020-04-01",
      "refdoctype": "Proforma Invoice",
      "refdocid": "PIN-2345763"
    },
    "shipmentdetails": {
      "vehiclenumber": "KA02-43-1244",
      "shipmentinvoice": "SH-5634522"
    },
    "itemdetails": [
      {
        "product": {
          "name": "Product-1",
          "desc": "This is my first item",
          "applicableTaxes": [
            "CGST",
            "SGST"
          ],
          "rate": 234.65,
          "unit": "packet"
        },
        "qty": 12,
        "hsncode": "HSC-2"
      },
      {
        "product": {
          "name": "Product-4",
          "desc": "This is my fourth product",
          "applicableTaxes": [],
          "rate": "NA",
          "unit": "crate",
          "isnew": true
        },
        "qty": 20,
        "hsncode": "HSC-4"
      }
    ]
  }
})
  }
</script>

<style scoped>
#pdfpreview{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
}

</style>