<template>
  <v-container id="Configuration" class="container--fluid">
    <div class="ConfigurationHeaders">
        <span style="display: flex"> Tax Configuration  </span>
    </div>
    
    <span >
        <h1 class="headline ml-12 mt-5 font-weight-medium">Select the taxes applicable to your company</h1>
    </span>
    <div class="form">
      <span v-for="(item, index) in availableTaxes" :key="index">
        <div class="row">
            <span :class="item.isSelected ? 'title optionName optionSelected' : 'title optionName' " > {{item.formalName}} [ {{item.shortName}} ]</span> 
            <span v-show="editindex != index"> <v-checkbox v-model="item.isSelected"  label="Is Applicable"  color="success" class="mt-n0"/> </span>
            <span v-show="editindex != index"> <v-btn x-small class="mt-5 ml-5" color="success" @click="editindex = index"> Edit Name </v-btn> </span>
            <span v-show="editindex === index"> <v-text-field  v-model="newFormalName" dense label="Formal Name" outlined class="mx-1 mt-3" :error="formalNameError" autocomplete="off"/> </span>
            <span v-show="editindex === index"> <v-text-field  v-model="newShortName" dense label="Short Name"  outlined class="mx-1 mt-3" maxlength="10" :error="shortNameError" autocomplete="off" @keydown.enter.prevent="SaveEdit"/> </span>
            <span v-show="editindex === index"> <v-btn class="mx-2 mt-3" color="success" @click="SaveEdit"> Save </v-btn> </span>
            <span v-show="editindex === index"> <v-btn class="mt-3" color="success" @click="CancelEdit"> Cancel </v-btn> </span>
        </div>
      </span>
        <div class="row justify-center mt-12 pb-8">
          <v-btn tile color="success" class="mx-2 my-2" :disabled='editindex != -1' @click="addNewTaxType"> <v-icon> mdi-plus</v-icon> Create New Tax Type </v-btn>
          <v-btn tile color="success" class="mx-2 my-2"> Save  </v-btn>
        </div>

        


    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'TaxConfig',
    watch:{
        'newFormalName': function(newval)
        {
            if(newval.trim() != '')
                this.formalNameError = false
        },
        'newShortName': function(newval)
        {
            if(newval.trim() != '')
                this.shortNameError = false
        }
    },
    computed:{
    },
    data: () => ({
        selected: true,
        editindex: -1,
        newFormalName: '',
        newShortName:'',
        formalNameError: false,
        shortNameError: false,
        availableTaxes: [{id: 1, shortName: 'SGST', formalName: 'State GST', isSelected: false}, {id: 2, shortName: 'CGST', formalName: 'Central GST', isSelected: false},
                        {id: 3, shortName: 'IGST', formalName: 'Integrated GST', isSelected: false}, {id: 4,shortName: 'CESS', formalName: 'CESS', isSelected: false}, 
                        {id: 5,shortName: 'VAT', formalName: 'Value Added Tax', isSelected: false}, {id: 6,shortName: 'Serv. Tax', formalName: 'Service Tax', isSelected: false}, 
                        {id: 7,shortName: 'EXCS', formalName: 'Excise Duty', isSelected: false}, {id: 8,shortName: 'CSTM', formalName: 'Custom Duty', isSelected: false}, 
                        {id: 9,shortName: 'CORP', formalName: 'Corporation Tax', isSelected: false},{id: 10,shortName: 'Sales Tax', formalName: 'Sales Tax', isSelected: false}, 
                        {id: 11,shortName: 'Entr. Tax', formalName: 'Entertainment Tax', isSelected: false}],
    }),
    methods: {
        SaveEdit(){
            if(this.newFormalName.trim() === '')
               this.formalNameError = true;
            else if(this.newShortName.trim() === '')
               this.shortNameError = true;
            else
            {
              this.availableTaxes[this.editindex].shortName = this.newShortName.trim();
              this.availableTaxes[this.editindex].formalName = this.newFormalName.trim();
              this.CancelEdit();
            }
        },
        CancelEdit(){
            if(Object.keys(this.availableTaxes[this.editindex]).length === 0)
                this.availableTaxes.splice(this.editindex, 1)
            this.newShortName = '';
            this.newFormalName = '';
            this.shortNameError = false;
            this.formalNameError = false;
            this.editindex = -1
        },
        addNewTaxType(){
            this.availableTaxes.push({});
            this.editindex = this.availableTaxes.length - 1;
        }
    }
    
}
</script>

<style scoped>
#Configuration{
    background-color: #ffffff;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.ConfigurationHeaders{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.ConfigurationHeaders > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    display: flex; 
    margin-top: 10px;
}

.optionName{
    padding-top:15px; 
    min-width: 300px; 
    color: grey;
}

.optionSelected{
    color: #202020;
    font-weight: bold;
}



@media only screen and (max-width: 1000px) {


    .row{
        display: flex;
        flex-direction: column;
    }

}


</style>