<template>
  <v-container id="newvoucher" class="container--fluid">
    <ReferanceDialog ref="getPartnerInfo" v-on:onClose="loaddata" />
    <DataLoadSpinner ref="spinner" />
    
    <div class="voucherheadlinetext">
        <span style="display: flex"> Delivery Challan </span>
    </div>
    
    <v-row class="mx-3 mt-6 mb-3" style="border-bottom: 2px solid grey">
        <span class="mx-3" style="font-size:1.6em; font-weight:500"> Challan Header</span> 
        <v-spacer></v-spacer> 
        <v-chip color="#FC570F" text-color="white" small class="mx-1 mt-2" v-show="headervalues.outstandingBalance >= 1"> O/s Due : {{$formatMoney(headervalues.outstandingBalance)}} </v-chip>
         <span class="mx-2 mt-2"> ** Indicates Required Field</span>
    </v-row>

    <!-- Header -->
    <v-row class="voucherheadersection">
      <!-- Header Row 1-->
        <v-row class="mx-4 mb-n3">
          <v-col cols="3">
            <v-text-field  dense :value="headervalues.partnername" label="Customer"  filled append-outer-icon="mdi-unfold-more-horizontal" no-resize readonly  @click:append-outer="changepartner" />
          </v-col>
          <v-col cols="3">
            <v-text-field dense :value="headervalues.partnertaxnum" label="Customer Tax Number"  filled disabled />
          </v-col>
          <v-col cols="3">
            <v-select  dense label="Challan Type **" autofocus v-model="headervalues.challantype" :items="challantype" hide-details :menu-props="{offsetY: true }" filled offset-y />
          </v-col>
          <v-col cols="3">
            <v-menu v-model="challandt" offset-y min-width="200px">
              <template v-slot:activator="{ on }" >
                <v-text-field  dense v-model="headervalues.challandate" label="Challan Date **"  filled append-icon="mdi-calendar"  @click:append="challandt = true" readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="headervalues.challandate" no-title @input="challandt = false" scrollable></v-date-picker>
            </v-menu>
          </v-col>
        </v-row>

      <!-- Billing Header Row 2-->
        <v-row class="mx-4 mt-n3">
          <v-col cols="3">
            <v-card outlined height="132px">
            <v-list>
             <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Contact Details <v-btn x-small color="info" class="float-right" @click="changecontactdetails" :disabled="partnercontact.length <= 1">Change</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1">
              <v-list-item-content>
                  <v-list-item-title class="font-weight-bold" style="font-size:0.95em"><v-icon small>mdi-account</v-icon> {{headervalues.contactdetails.name}}</v-list-item-title>
                  <v-list-item-subtitle class="my-2"> <v-icon small>mdi-phone</v-icon> <span style="font-size:0.95em"> {{headervalues.contactdetails.phone}} </span></v-list-item-subtitle>
                  <v-list-item-subtitle> <span style="display: inline-flex;"><v-icon small class="mr-2">mdi-email</v-icon>  <span style="font-size:0.95em"> {{headervalues.contactdetails.email}} </span></span> </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            </v-list>
            </v-card>
          </v-col>
          <v-col cols="3">
            <!--<v-textarea  filled label="Billed Address" rows="4" row-height="50" append-outer-icon="mdi-unfold-more-horizontal" no-resize readonly></v-textarea>-->
             <v-card outlined height="132px">
            <v-list>
            <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Shipping Address <v-btn x-small color="info" class="float-right" @click="changeaddressdetails" :disabled="partneraddress.length <= 1">Change</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1" v-if="Object.keys(headervalues.address).length != 0">
              <v-list-item-content>
                  <v-list-item-subtitle> {{ [headervalues.address.addressline, headervalues.address.postcode].join(", ") }} </v-list-item-subtitle>
                  <v-list-item-subtitle class="font-weight-bold"> {{ [headervalues.address.city, headervalues.address.state, headervalues.address.country].join(", ") }}  </v-list-item-subtitle>
               </v-list-item-content>
            </v-list-item>
            </v-list>
            </v-card>
          </v-col>
          <v-col cols="3">
            <v-select  dense label="Reference Document Type" v-model="headervalues.refdoctype" :items="refdoctypes" hide-details :menu-props="{offsetY: true }" filled offset-y />
          </v-col> 
          <v-col cols="3">
            <v-text-field  dense label="Reference Document Number#" v-model="headervalues.refdocid" filled autocomplete="off"/>
          </v-col>          
        </v-row>
    </v-row>

    <!-- Billing Details -->
    <v-row class="mx-3 mt-3" style="border-bottom: 2px solid grey;">
        <span class="mx-3" style="font-size:1.6em; font-weight:500"> Item Details</span>
    </v-row>

  <!-- Billing item details table-->
    <v-simple-table class="mx-3" style="border-bottom: 2px solid grey; border-radius: 1px; margin-bottom: 2px" >
      <thead>
        <tr>
          <th class="text-center">#</th>
          <th class="text-left" >Product</th>
          <th class="text-center">HSN/SAC Code</th>
          <th class="text-center">Quantity</th>
          <th class="text-center">Unit</th>
          <th class="text-center">Rate</th>
          <th class="text-center">Modify</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in billingItems" :key="index" >
          <td class="text-center" >{{index + 1}}</td>
          <!--<td>{{ item.prdName }}</td> -->
          <td class="text-left" style="border-left: 1px solid #dddddd">
            <v-list-item two-line>
              <v-list-item-content>
                  <v-list-item-title class="font-weight-bold">{{item.product.name}} <v-chip x-small color="#38D525" class="mb-1" v-if="item.product.isnew"> new </v-chip></v-list-item-title>
                  <label style="max-width: 350px; color: grey; font-size: 1em">{{item.product.desc}}</label>
              </v-list-item-content>
            </v-list-item>
          </td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.hsncode }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.qty }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.product.unit }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.product.rate }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd"><v-icon color="red" @click="deleteItem(index)"> mdi-delete </v-icon></td>
        </tr>
        <tr style="background-color: #F2F2F2">
        <td class="text-center"><v-icon class="mx-n2">mdi-star-box</v-icon></td>
        <td  style="border-left: 1px solid #dddddd">
            <v-list-item two-line>
              <v-list-item-content>
                  <v-list-item-title class="font-weight-medium">
                    <v-combobox :items="products" item-text="name" item-value="desc" class="mt-2"
                        label="Product **" dense outlined flat v-model="newItem.product"
                        :error="formError.product"
                        ref="productname"
                        :error-messages="formError.product ? `Select the product or Enter new product name` : ''" /></v-list-item-title>
                        <v-textarea solo flat rows="3" outlined class="my-n6" placeholder="Product Description" label=""
                        v-model="newItem.product.desc" style="font-size: 0.9em;" 
                        :disabled="newItem.product.name ? false : true"></v-textarea>
              </v-list-item-content>
            </v-list-item>
        </td>
        <td  style="border-left: 1px solid #dddddd">
            <v-combobox flat dense solo outlined 
            v-model="newItem.hsncode" 
            :disabled="newItem.product.name ? false : true" />
        </td>
        <td  style="border-left: 1px solid #dddddd">
            <v-text-field placehold="Quantity **" dense solo flat outlined 
            v-model="newItem.qty" 
            v-on:keypress="$isNumber" 
            :error="formError.qty"
            :error-messages="formError.qty ? `Quantity cannot be 0` : ''"
            :disabled="newItem.product.name ? false : true" />
        </td>
        <td  style="border-left: 1px solid #dddddd">
            <v-select :items="units" :menu-props="{offsetY: true }" 
            dense solo flat outlined v-model="newItem.product.unit" 
            :disabled="newItem.product.name ? false : true"
            :error="formError.unit"
            :error-messages="formError.unit ? `Unit cannot be empty` : ''"
            />
        </td>
        <td  style="border-left: 1px solid #dddddd">
            <v-text-field label="Rate" dense solo flat outlined 
            v-model="newItem.product.rate" v-on:keypress="$isNumber"  
            :disabled="newItem.product.name ? false : true"  @keyup.enter="addItem"/> 
        </td>
        <td  style="border-left: 1px solid #dddddd">
            <v-btn fab dark color="success" small @click="addItem">
              <v-icon dark>mdi-plus</v-icon>
            </v-btn>
        </td>
        </tr>
      </tbody>
  </v-simple-table>
    
    <!-- Billing footer -->
    <v-row class="mx-3 mt-0">
      <v-col cols="5">
        <v-textarea label="Comments" rows="4" outlined v-model="comments"></v-textarea>
      </v-col>
      <v-col cols="1">
      </v-col>

      <v-col>
        <v-row class="mt-n2"> 
            <v-col cols="3" class="mt-2"> <v-label> Vehicle Number  </v-label> </v-col>
            <v-col cols="1" class="mt-2">: </v-col>
            <v-col cols="6"> <v-text-field dense v-model="shipmentDetails.vehiclenumber"/> </v-col>
        </v-row>
        <v-row class="mt-n3"> 
            <v-col cols="3" class="mt-2"> <v-label> Shipment Invoice # </v-label> </v-col>
            <v-col cols="1" class="mt-2">: </v-col>
            <v-col cols="6"> <v-text-field dense v-model="shipmentDetails.shipmentinvoice"/> </v-col>
        </v-row>
      </v-col>
    
    </v-row>
    
    <v-row class="mx-3 pr-3 mt-3 pb-1">
      <v-spacer></v-spacer>
      <v-btn large color="primary" class="mx-2" @click="discard(false)">Discard</v-btn>
      <v-btn large color="primary" class="mx-2" :disabled="billingItems.length <= 0">Save as Draft</v-btn>
      <v-btn large color="primary" class="mx-2" @click="saveandpreview" :disabled="billingItems.length <= 0">Save & Preview</v-btn>
    </v-row>

    <v-dialog v-model="discarddialog"  max-width="250">
      <v-card>
        <v-card-title class="headline">Discard</v-card-title>
        <v-card-text>Are you sure you want to discard this transaction? </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="discarddialog = false;  discard(true)" >  YES  </v-btn>
          <v-btn color="green darken-1" text @click="discarddialog = false" >  NO     </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <ChooseContactDetails ref="changecontactdetailsdialog" v-on:onClose="newcontactdetails" />
    <ChooseAddressDetails ref="changeaddressdetailsdialog" v-on:onClose="newaddressdetails" />

  </v-container>
</template>

<script>
import ReferanceDialog from '@/components/ReferanceDialog.vue'
import DataLoadSpinner from '@/components/DataLoadSpinner.vue'
import ChooseContactDetails from '@/components/ChooseContactDetails.vue'
import ChooseAddressDetails from '@/components/ChooseAddressDetails.vue'

  export default {
    name: 'CreateDeliveryChallan',
    components: {
      ReferanceDialog,
      DataLoadSpinner,
      ChooseContactDetails,
      ChooseAddressDetails
    },
    watch:{
      'newItem.product' : function(newVal){
         this.newItem.product = typeof newVal != 'object' ? {name: newVal.trim(), desc : null, rate: 'NA', unit: null, isnew: true} : newVal;
         this.formError.product = false;
      },
      'newItem.qty' : function(newVal){
         this.newItem.qty = isNaN(parseFloat(newVal,10)) ? 0 : parseFloat(newVal,10);
         if(this.newItem.qty == 0)
          this.formError.qty = true
          else
          this.formError.qty = false
      },
      'newItem.product.rate' : function(newVal){
         this.newItem.product.rate = isNaN(parseFloat(newVal,10)) ? 'NA' : parseFloat(newVal,10);
      },
      'newItem.product.unit' : function(){
          this.formError.unit = false;
      }
    },
    computed:{
        refdoctypes(){
            return this.$store.getters.getDCRefDocTypes;
        },
        challantype(){
            return this.$store.getters.getDCChallanType;
        },
        products(){
            return this.$store.getters.getProductDetails;
        },
        units(){
            return this.$store.getters.getAvailableUoM;
        }
    },
    data : () => ({
        addressdialog: false,
        contactdialog: false,
        discarddialog: false,
        challandt : false,
        formError:{
          product: false,
          qty : false,
          unit: false
        },
        partneraddress:[],
        partnercontact:[],

        headervalues:{
          partner:'',
          partnertaxnum:'',
          outstandingBalance:null,
          contactdetails:{},
          address:{},
          challantype: null,
          challandate: new Date().toISOString().substring(0,10),
          refdoctype: null,
          refdocid: null          
        },
        billingItems:[],
        newItem:{
          product : {
                        name: null, 
                        desc: null, 
                        unit : null, 
                        rate: 'NA', 
                        isnew: false
                    }, 
          qty : 1,
          hsncode: null

        },
        comments: null,
        shipmentDetails:{
            vehiclenumber: null,
            shipmentinvoice: null
        }
        
    }),
    methods: {
      changecontactdetails: function(){ 
          this.$refs.changecontactdetailsdialog.show(this.partnercontact, this.headervalues.partnername)   
      },
      newcontactdetails: function(data){
          this.headervalues.contactdetails = data;
      },
      changeaddressdetails: function(){ 
          this.$refs.changeaddressdetailsdialog.show(this.partneraddress, this.headervalues.partnername)   
      },
      newaddressdetails: function(data){ 
          this.headervalues.address = data; 
      },
      addItem : function(){
        
        //perform some validation
        if(this.newItem.product.name == null || this.newItem.product.name == undefined || this.newItem.product.name.trim() == '')
          this.formError.product = true
        else if(this.newItem.qty <= 0)
          this.formError.qty = true
        else if(!this.newItem.product.unit)
          this.formError.unit = true
        else
        {
          this.billingItems.push(Object.assign({},this.newItem));
          this.resetItemEntry();
          this.scrollToEnd();
          this.$refs.productname.focus()
        }
      },
      deleteItem: function(index){
        this.billingItems.splice(index,1);
      },
      resetItemEntry: function(){
          this.newItem.product =   {
                                    name: null, 
                                    desc: null, 
                                    unit : null, 
                                    rate: 'NA', 
                                    isnew: false
                                }, 
          this.newItem.qty = 1;
          this.newItem.hsncode = null;
      },

      discard: function(value)
      {
     
        if(value == true)
        {
        this.partneraddress = [],
        this.partnercontact = [],
        this.billingItems=[],
        this.newItem={
                product : {
                        name: null, 
                        desc: null, 
                        unit : null, 
                        rate: 'NA', 
                        isnew: false
                    }, 
                qty : 1,
                hsncode: null
            },
        this.comments = null,
        this.shipmentDetails ={
                                vehiclenumber: null,
                                shipmentinvoice: null
                            }
        this.changepartner()
        }
        else
          this.discarddialog = true;
      },

      scrollToEnd: function(){
        this.$nextTick( () =>{ document.documentElement.scrollTop = document.documentElement.scrollHeight});
      },

      saveandpreview(){
        this.$router.push({name: 'DeliveryChallanPreview',  query: { "challan": 'DC-1234' } })
      },

      loaddata(data)
      {
        this.$refs.getPartnerInfo.close()
        this.$refs.spinner.show();
        setTimeout(()=>{
          this.$refs.spinner.close();
            this.headervalues.partnername = data.name,
            this.outstandingBalance = 12345678.91;
            this.headervalues.contactdetails = {id: 1, name:'Girish',phone:'+49-143254648363',email:'girish.1243@gmail.com'},
            this.headervalues.address = {addressline:'Garching 23', postcode: '80638', city: 'Munich', state:'Bavaria', country: 'Germany' },
            this.headervalues.partnertaxnum = "AGSJY7-HJAH2-GHSYT-21SGD"
            this.partneraddress.push({addressline:'Leopardstrasse 43', postcode: '80638', city: 'Munich', state:'Bavaria', country: 'Germany' });
            this.partneraddress.push({addressline:'475 Vijayanagar', postcode: '560077', city: 'Bengaluru', state:'Karnataka', country: 'INDIA' });
            this.partneraddress.push({addressline:'Leopardstr 31', postcode: '80453', city: 'Berlin', state:'Brandenburg', country: 'Germany' });
            this.partnercontact.push({id: 1, name:'Girish',phone:'+49-16123672234',email:'girish.123455@gmail.com'});
            this.partnercontact.push({id: 2, name:'Mahesh',phone:'+49-16123672234',email:'girish.133243@gmail.com'});
            this.partnercontact.push({id: 3, name:'Girish BN',phone:'+91-99342527384',email:'connect_girish@gmail.com'});
            
        }, 500)
      },
      changepartner(){
        this.$refs.getPartnerInfo.show('Customer','Delivery Challan');
      }
    },
    mounted (){
      this.$refs.getPartnerInfo.show('Customer','Delivery Challan');
    }
  }
</script>

<style scoped>
#newvoucher{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
}

.voucherheadlinetext{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.voucherheadlinetext > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

</style>