<template>
  <v-container id="Configuration" class="container--fluid">
    <div class="ConfigurationHeaders">
        <span style="display: flex"> Payment Configuration  </span>
    </div>
    
    <span >
        <h1 class="headline ml-12 mt-5 font-weight-medium">Select / Create your payment details to accept payments from vendors.</h1>
    </span>
    <div class="form">
    <span v-for="(item, index) in paymentdetails" :key="index">
        <div class="row mb-2">
            <span class='title optionName' > {{item.type}} </span> 
            <span > <v-checkbox label="Is Applicable"  color="success" class="mt-n0" v-model="item.isSelected"/> </span>
        </div>
        <span v-show="item.details && item.isSelected" v-for="(itemDetails, idx) in item.details" :key="idx">
           <div class="row ">
              <span class="row-small-label"><v-label> {{itemDetails.label}} </v-label></span>
              <span class="row-small-text"><v-text-field autofocus class="" dense solo outlined :placeholder="itemDetails.sampleText" v-model="itemDetails.value"/></span>
           </div>
        </span>
        <div class="row mt-n1" v-show="item.multipleValue === true && item.details && item.isSelected">
            <span class="row-small-text" @click="addnewtype(index)"><v-btn block tile small color="info"><v-icon small> mdi-plus </v-icon> Add New {{item.type}} Details </v-btn></span>
        </div>
        <div class="row mt-n1" v-show="item.deletable === true && item.isSelected">
            <span class="row-small-text" @click="deleteType(index)"><v-btn block tile small color="red"><v-icon small> mdi-minus </v-icon> Remove this payment type </v-btn></span>
        </div>
    </span>
        <div class="row justify-center mt-12 pb-8">
          <v-btn tile color="success" class="mx-2 my-2" @click="addNewCustomPaymentType"> <v-icon> mdi-plus</v-icon> Create New Payment Type </v-btn>
          <v-btn tile color="success" class="mx-2 my-2"> Save  </v-btn>
        </div>

    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'PaymentTypeConfig',
    watch:{
    },
    computed:{
    },
    data: () => ({
        paymentdetails: [
           {
               "type": "Cash",
               "isSelected" : false
           },
           {
               "type": "Cheque",
               "isSelected" : false,
               "details":[{
                   label: "Issues in the name of",
                   sampleText: "Enter Name",
                   value: ""
               }]
           },
           {
               "type": "Bank Transfer",
               "isSelected" : false,
               "multipleValue" : true,
               "details":[{
                   label: "Type",
                   sampleText: "NEFT / RTGS",
                   value: ""
               },
               {
                   label: "Account Holder Name",
                   sampleText: "Name",
                   value: ""
               },
               {
                   label: "Account Number",
                   sampleText: "xxxxxxxxxxxxxxxxxxx",
                   value: ""
               },
               {
                   label: "IFSC Code",
                   sampleText: "Bank IFSC Code",
                   value: ""
               }]
           },
           {
               "type": "UPI",
               "isSelected" : false,
               "multipleValue" : true,
               "details":[{
                   label: "Vendor",
                   sampleText: "PayTM / G-Pay / Phonepe / SBI**",
                   value: ""
               },
               {
                   label: "ID",
                   sampleText: "Phone Number / UPI Address",
                   value: ""
               },
               ]
           },
           {
               "type": "Paypal",
               "isSelected" : false,
               "details":[{
                   label: "Paypal ID",
                   sampleText: "Paypal Address",
                   value: ""
               }]
            }
        ]
    }),
    methods: {
        addnewtype(index){
            let tmp = JSON.parse(JSON.stringify(this.paymentdetails[index]));
            tmp.isSelected = true;
            tmp.multipleValue = false;
            tmp.deletable = true;
            tmp.details.map(x => x.value = "");
            this.paymentdetails.splice(index+1, 0, tmp);
        },
        deleteType(index){
            this.paymentdetails.splice(index, 1);
        },
        addNewCustomPaymentType()
        {
            this.paymentdetails.push(
                {
                    "type": "Custom",
                    "isSelected" : true,
                    "deletable" : true,
                    "details":[{
                        label: "Name",
                        sampleText: "What do you want to call this as?",
                        value: ""
                     },
                     {
                        label: "ID",
                        sampleText: "Id",
                        value: ""
                     },
                     {
                        label: "Description",
                        sampleText: "Details about this payment type",
                        value: ""
                     }]
                }
            )
        }
    }
    
}
</script>

<style scoped>
#Configuration{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.ConfigurationHeaders{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.ConfigurationHeaders > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    display: flex; 
    margin-top: 10px;
}

.optionName{
    padding-top:15px; 
    min-width: 300px; 
    color: grey;
}

.row-small-label{
    width: 160px;
    margin-left: 40px;
    margin-top: 8px;
}

.row-small-text{
    width: 40%;
    margin-left: 40px;
}


@media only screen and (max-width: 1000px) {

    .row{
        display: flex;
        flex-direction: column;
    }

    .row-small-label{
        margin-top: -20px;
        margin-bottom: 5px;
        width: 100%;
        margin-left: 0px;
    }

    .row-small-text{
        width: 100%;
        margin-left: 0px;
    }

}


</style>