<template>
  <v-container id="VoucherEntry" class="container--fluid">
    <div class="VoucherEntryHeader">
        <span style="display: flex"> Bill Entry  </span>
    </div>
    
    <div class="form">
        <div class="row">
            <span class="row-small-component">
                <v-label> Bill Type </v-label>
                <v-select  :items="availableBillTypes" :menu-props="{offsetY: true }" placeholder="Select Bill type" v-model="billinfo.billtype" class="textbox font-weight-medium mt-1 ml-n1" outlined solo dense  flat/>
            </span>
            <span class="row-small-component" v-show="newBillType">
                <v-label> Enter New Bill Type </v-label>
                <v-text-field placeholder="Enter New Bill Type" v-model="billinfo.customBilltype" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        <div class="row">
            <span class="row-small-component">
                <v-label> Vendor / Supplier </v-label>
                <v-text-field placeholder="Vendor Name" v-model="billinfo.vendorName" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
            <span class="row-small-component">
                <v-label> Vendor Tax Number </v-label>
                <v-text-field placeholder="Tax Number" v-model="billinfo.vendorTaxNumber" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        <div class="row">
            <span class="row-large-component">
                <v-label> Vendor Address </v-label>
                <v-text-field placeholder="Vendor Address" v-model="billinfo.vendorAddress" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        <div class="row">
            <span class="row-small-component">
                <v-label> Vendor Bill Number </v-label>
                <v-text-field placeholder="Bill Number" v-model="billinfo.billNumber" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
            <span class="row-small-component">
                <v-label> Bill Sumbitted by </v-label>
                <v-text-field placeholder="Submitted by (Name)" v-model="billinfo.billSubmittedBy" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        <div class="row">
            <span class="row-small-component">
                <v-label> Bill Date </v-label>
                <v-menu v-model="billdate" offset-y min-width="200px">
                    <template v-slot:activator="{ on }">
                    <v-text-field placeholder="Payment Due Date" v-model="billinfo.billingDate" class="textbox font-weight-medium mt-1 ml-n1" append-icon="mdi-calendar" @click:append="billdate = true" readonly v-on="on" outlined solo dense flat/>
                    </template>
                    <v-date-picker v-model="billinfo.billingDate" no-title @input="billdate = false;" scrollable></v-date-picker>
                </v-menu>
            </span>
            <span class="row-small-component">
                <v-label> Due Date </v-label>
                <v-menu v-model="duedate" offset-y min-width="200px">
                    <template v-slot:activator="{ on }">
                    <v-text-field placeholder="Payment Due Date" v-model="billinfo.paymentDueDate" class="textbox font-weight-medium mt-1 ml-n1" append-icon="mdi-calendar" @click:append="duedate = true" readonly v-on="on" outlined solo dense flat/>
                    </template>
                    <v-date-picker v-model="billinfo.paymentDueDate" no-title @input="duedate = false;" scrollable></v-date-picker>
                </v-menu>
            </span>
        </div>

        <div class="row">
            <span class="row-small-component">
                <v-label> Net Amount </v-label>
                <v-text-field placeholder="Net Amount" v-model="billinfo.netAmount" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
            <span class="row-small-component">
                <v-label> Tax Applied </v-label>
                <v-select  :items="availableTaxTypes" :menu-props="{offsetY: true }" placeholder="Select Bill type" v-model="billinfo.taxApplied" class="textbox font-weight-medium mt-1 ml-n1" multiple outlined solo dense  flat/>
            </span>
        </div>

        <div class="row">
            <span v-for="(item, index) in billinfo.taxApplied" :key="index">
            <span class="row-x-small-component">
                <v-label> {{item}}</v-label>
                <v-text-field :placeholder="item" v-model="billinfo.netAmount" class="textbox font-weight-medium mt-1 ml-n1" 
                    autocomplete=false outlined solo dense flat v-on:keypress="$isNumber" :append-icon="taxinpercentage ? 'mdi-percent' : 'mdi-numeric'" 
                    @click:append="taxinpercentage = !taxinpercentage" />
            </span>
            </span>
        </div>

        <div class="row">
            <span class="row-large-component mx-2">
                <v-label> Gross Amount </v-label>
                <v-text-field placeholder="Gross Amount" v-model="billinfo.grossAmount" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        <div class="row">
            <span class="row-large-component mx-2">
                <v-label> Comments </v-label>
                <v-textarea placeholder="Enter comments or description of the bill" v-model="billinfo.comments" class="textbox font-weight-medium mt-1 ml-n1" autocomplete=false outlined solo dense flat/>
            </span>
        </div>

        

        
        <div class="row justify-center mt-6 pb-5"> 
        <span class="row-small-component mx-2">
            <v-btn block color="#BFBFBF" tile > Reset  </v-btn>
        </span>
        <span class="row-small-component mx-2">
            <v-btn block color="success" tile> Save  </v-btn>
        </span>
        </div>

    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'BillEntry',
    watch:{
        'billinfo.billtype': function(newval)
        {
            if(newval === 'Others')
            {
                this.newBillType = true;
            }
            else
                this.newBillType = false;
        }
    },
    computed:{
      availableBillTypes(){
            return this.$store.getters.getBillingTypes;
        },
      availableTaxTypes(){
            return this.$store.getters.getAvailableTaxes;
        }
    },
    data: () => ({
        newBillType: false,
        duedate: false,
        billdate: false,
        taxinpercentage: true,
        billinfo:{
            billType: '',
            customBilltype: '',
            vendorName: '',
            vendorTaxNumber: '',
            vendorAddress: '',
            billNumber: '',
            billSubmittedBy: '',
            billingDate: '',
            paymentDueDate: '',
            netAmount:'',
            taxApplied: '',
            grossAmount: '',
            comments: ''
        }
    }),
    methods: {
    }
    
}
</script>

<style scoped>
#VoucherEntry{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.VoucherEntryHeader{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.VoucherEntryHeader > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    display: flex; 
    margin-top: 10px;
}

.row .row-large-component{
    display: block; 
    width: 92%;
    margin: 0px 10px;
}

.row .row-small-component{
    display: block; 
    width: 45%;
    margin: 0px 10px;
}

.row .row-x-small-component{
    display: block; 
    width: 150px;
    margin: 0px 10px;
}



@media only screen and (max-width: 1000px) {

    .row{
        display: flex;
        flex-direction: column;
    }

    .row .row-small-component, .row .row-large-component{
        min-width: 100%;
        max-width: 100%;
    }
}


</style>