<template>
  <v-row justify="center" >
    <v-dialog v-model="displayDialog" max-width="850" >
      <v-card shaped>
        <v-card-title style="font-size: 1.2em; background-color: #0AB2BF; color: white" color="red" >Select Address Details</v-card-title>
        <v-divider />
        
        <v-layout justify-start align-center class="mt-10"> 
          <div class="headline pl-12"> {{partnerName}} </div>
        </v-layout>
        <v-layout justify-center align-center class="mt-1">           
        <span class="contentlist" style="width: 85%; display: flex;  flex-wrap: wrap; justify-content: space-between;">
        <span v-for="(item, index) in addressdetails" :key="index">
          <v-card outlined height="125px" width="350px" class="mt-2">
            <v-list>
             <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Address Details <v-btn x-small color="info" class="float-right" @click="selectAddress(index)">Select</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1">
              <v-list-item-content>
                  <v-list-item-subtitle> {{ [item.addressline, item.postcode].join(", ") }} </v-list-item-subtitle>
                  <v-list-item-subtitle class="font-weight-bold"> {{ [item.city, item.state, item.country].join(", ") }}  </v-list-item-subtitle>
               </v-list-item-content>
            </v-list-item>
            </v-list>
          </v-card> 
        </span>            
        </span>
        </v-layout>

        <v-layout justify-center align-center class="pt-4 mb-n3 mx-12" v-show="newentry" row>  
          <div style="display: flex;width:100%"> 
            <v-select ref="name"  @keyup.enter="saveAddress" :error="validate && newaddress.country.trim() === ''" dense  label="Country" class="mx-1" v-model="newaddress.country" :menu-props="{offsetY: true }" :items="availablecountries"/>
            <v-select ref="phone" @keyup.enter="saveAddress" :error="validate && newaddress.state.trim() === ''" dense  label="State" class="mx-1" v-model="newaddress.state" :menu-props="{offsetY: true }" :items="getavailableState"/>
            <v-text-field ref="email" @keyup.enter="saveAddress" :error="validate && newaddress.city.trim() === ''" dense  flat label="City" class="mx-1" v-model="newaddress.city" autocomplete="off"/>
          </div>
          <div style="display: flex; width:100%"> 
            <v-text-field ref="phone" @keyup.enter="saveAddress" :error="validate && newaddress.postcode.trim() === ''" dense label="Post Code" class="mx-1" v-model="newaddress.postcode" style="max-width: 150px" autocomplete="off"/>
            <v-text-field ref="email" @keyup.enter="saveAddress" :error="validate && newaddress.addressline.trim() === ''" dense label="Address Line" class="mx-1" v-model="newaddress.addressline" autocomplete="off"/>
          </div>
        </v-layout>
        <v-layout justify-center align-center class="mt-3 pb-12 mx-12" v-show="newentry">  
          <v-btn tile color="success" block @click="saveAddress" > Save Details </v-btn>
        </v-layout>

        <v-layout justify-center align-center class="pt-4 pb-12 mx-12" v-show="!newentry">  
              <v-btn tile color="success" block @click="newentry = true" >Add New Address Details</v-btn>
        </v-layout>

      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    computed:{
      availablecountries(){
            return this.$store.getters.getCountries
        },
      getavailableState(){
            return this.$store.getters.getState(this.newaddress.country)
        }
    },
    data () {
      return {
        displayDialog: false,
        addressdetails: [],
        validate: false,
        partnerName: null,
        newentry: false,
        newaddress:{
              addressline: '',
              postcode: '',
              city: '',
              state: '',
              country: '',
            },
      }},
      methods: {
        show(addressdetails, partnerName){
          if(addressdetails || partnerName)
          {
            this.displayDialog = true;
            this.partnerName = partnerName;
            this.addressdetails = addressdetails;
          }
        },
        close(){
          this.displayDialog = false;
        },
        selectAddress(index){
          this.$emit('onClose', this.addressdetails[index]);
          this.newaddress = {
              addressline: '',
              postcode: '',
              city: '',
              state: '',
              country: '',
            };
            this.validate = false;
            this.newentry = false;
          this.close()
        },
        saveAddress()
        {
          this.validate = true;
          if(this.newaddress.addressline.trim() !== '' && this.newaddress.postcode.trim() !== '' && this.newaddress.city.trim() !== '' && this.newaddress.state.trim() !== '' && this.newaddress.country.trim() !== '')
          {
            this.addressdetails.push(this.newaddress);
            this.newaddress = {
              addressline: '',
              postcode: '',
              city: '',
              state: '',
              country: '',
            };
            this.validate = false;
            this.newentry = false;
          }    

        }
    }
  }
</script>

<style scoped>

span{
  user-select:none;
}

.contentlist{
  max-height: 320px;
  overflow-y: scroll;
}

.contentlist::-webkit-scrollbar {
  width: 3px;
}
 
.contentlist::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
}
 
.contentlist::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

</style>