<template>
  <v-container id="newvoucher" class="container--fluid">
    <ReferanceDialog ref="getPartnerInfo" v-on:onClose="loaddata" />
    <DataLoadSpinner ref="spinner" />
    
    <div class="voucherheadlinetext">
        <span style="display: flex"> Tax Invoice </span>
    </div>
    
    <v-row class="mx-3 mt-6 mb-3" style="border-bottom: 2px solid grey">
        <span class="mx-3" style="font-size:1.6em; font-weight:500"> Invoice Header</span> 
        <v-spacer></v-spacer> 
        <v-chip color="#FC570F" text-color="white" small class="mx-1 mt-2" v-show="headervalues.outstandingBalance >= 1"> O/s Due : {{$formatMoney(headervalues.outstandingBalance)}} </v-chip>
         <span class="mx-2 mt-2"> ** Indicates Required Field</span>
    </v-row>

    <!-- Header -->
    <v-row class="voucherheadersection">
      <!-- Header Row 1-->
        <v-row class="mx-4 mb-n3">
          <v-col cols="3">
            <v-text-field  dense :value="headervalues.partnername" label="Customer"  filled append-outer-icon="mdi-unfold-more-horizontal" no-resize readonly  @click:append-outer="changepartner" />
          </v-col>
          <v-col cols="3">
            <v-text-field dense :value="headervalues.partnertaxnum" label="Customer Tax Number"  filled disabled />
          </v-col>
          <v-col cols="3">
            <v-select  dense label="Credit Memo#" autofocus v-model="headervalues.creditmemo" :items="creditmemolist" hide-details no-data-text="Credit Memo Not Available" :menu-props="{offsetY: true }" filled offset-y />
          </v-col>
          <v-col cols="3">
            <v-select  dense label="Delv. Challan#" v-model="headervalues.delvchaln" :items="delvchalnlist" hide-details no-data-text="No Delv. Challan Available" :menu-props="{offsetY: true }" filled offset-y />
          </v-col>
        </v-row>

      <!-- Header Row 2-->
        <v-row class="mx-4 mt-n3">
          <v-col cols="3">
            <v-card outlined height="132px">
            <v-list>
             <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Contact Details <v-btn x-small color="info" class="float-right" @click="changecontactdetails" :disabled="partnercontact.length <= 1">Change</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1">
              <v-list-item-content>
                  <v-list-item-title class="font-weight-bold" style="font-size:0.95em"><v-icon small>mdi-account</v-icon> {{headervalues.contactdetails.name}}</v-list-item-title>
                  <v-list-item-subtitle class="my-2"> <v-icon small>mdi-phone</v-icon> <span style="font-size:0.95em"> {{headervalues.contactdetails.phone}} </span></v-list-item-subtitle>
                  <v-list-item-subtitle> <span style="display: inline-flex;"><v-icon small class="mr-2">mdi-email</v-icon>  <span style="font-size:0.95em"> {{headervalues.contactdetails.email}} </span></span> </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            </v-list>
            </v-card>
          </v-col>
          <v-col cols="3">
            <v-card outlined height="132px">
            <v-list>
            <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Billing Address <v-btn x-small color="info" class="float-right" @click="changeaddressdetails" :disabled="partneraddress.length <= 1">Change</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1" v-if="Object.keys(headervalues.address).length != 0">
              <v-list-item-content>
                  <v-list-item-subtitle> {{ [headervalues.address.addressline, headervalues.address.postcode].join(", ") }} </v-list-item-subtitle>
                  <v-list-item-subtitle class="font-weight-bold"> {{ [headervalues.address.city, headervalues.address.state, headervalues.address.country].join(", ") }}  </v-list-item-subtitle>
               </v-list-item-content>
            </v-list-item>
            </v-list>
            </v-card>
          </v-col>
          <v-col cols="3">
            <v-select  dense label="Terms and Conditions" v-model="headervalues.terms"        :items="termslist"       hide-details no-data-text="Terms & Cond. not defined" :menu-props="{offsetY: true }" filled offset-y />
            <v-select  dense label="Payment Type"         v-model="headervalues.paymenttypes" :items="paymenttypelist" hide-details no-data-text="Payment types not defined" :menu-props="{offsetY: true }" filled offset-y multiple class="mt-6" />
          </v-col> 
          <v-col cols="3">

            <v-menu v-model="invdt" offset-y min-width="200px">
              <template v-slot:activator="{ on }">
                <v-text-field  dense v-model="headervalues.invoicedate" label="Invoice Date **"  filled append-icon="mdi-calendar"  @click:append="invdt = true" readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="headervalues.invoicedate" no-title @input="invdt = false" scrollable></v-date-picker>
            </v-menu>

            <v-menu v-model="duedt" offset-y min-width="200px">
              <template v-slot:activator="{ on }">
                <v-text-field  dense v-model="headervalues.invoiceduedate" label="Payment Due Date **"  filled append-icon="mdi-calendar"  @click:append="duedt = true" readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="headervalues.invoiceduedate" no-title @input="duedt = false" scrollable></v-date-picker>
            </v-menu>

          </v-col>          
        </v-row>
    </v-row>

    <!-- Invoice Details Label-->
    <v-row class="mx-3" style="border-bottom: 2px solid grey;">
        <span class="mx-3" style="font-size:1.6em; font-weight:500"> Item Details</span>
    </v-row>

  <!-- Invoice item details table-->
    <v-simple-table class="mx-3" style="border-bottom: 2px solid grey; border-radius: 1px; margin-bottom: 2px" >
      <thead>
        <tr>
          <th class="text-center">#</th>
          <th class="text-left" >Product</th>
          <th class="text-center">Quantity</th>
          <th class="text-center">Unit</th>
          <th class="text-center">Rate</th>
          <th class="text-center">Discount</th>
          <th class="text-center">Tax</th>
          <th class="text-center">Amount</th>
          <th class="text-center">Modify</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in billingItems" :key="index" >
          <td class="text-center" >{{index + 1}}</td>
          <td class="text-left" style="border-left: 1px solid #dddddd">
            <v-list-item two-line>
              <v-list-item-content>
                  <v-list-item-title class="font-weight-bold">{{item.productName}} <v-chip x-small color="#38D525" class="mb-1" v-if="item.isnew"> new </v-chip></v-list-item-title>
                  <label style="max-width: 350px; color: grey; font-size: 1em">{{item.productDesc}}</label>
              </v-list-item-content>
            </v-list-item>
          </td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.qnty }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.unit }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.rate }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ $formatMoney(item.discount.numericvalue) }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ item.totalTaxPercentage }} %</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">{{ $formatMoney(item.grossAmount) }}</td>
          <td class="text-center" style="border-left: 1px solid #dddddd">
            <v-icon color="success" @click="editBillItemFunc(index)"> mdi-pencil-circle </v-icon>
            <v-icon color="red" @click="deleteItem(index)"> mdi-delete </v-icon>
          </td>
        </tr>
      </tbody>
    </v-simple-table>
    
    <BillingItemEntryForm class="ml-3" v-on:newItem="insertNewItem" :recordnumber="billingItems.length" isNew style="border-bottom: 2px solid grey; padding-bottom: 10px"/>
    <v-dialog v-model= "editBillItem" max-width="80%" persistent >
        <v-card class="py-2">
            <BillingItemEntryForm class="ml-4" v-on:cancelEdit="editBillItem = false" v-on:newItem="saveChangedBillItem" :recordnumber="editbillitemindex + 1" ref="edititemdialog" />
        </v-card>
    </v-dialog>

    
    
    <!-- Billing footer 
    <v-row class="mx-3 mt-0">
      <v-col cols="3">
        <v-textarea label="Comments" rows="4" outlined v-model="summary.comments"></v-textarea>
      </v-col>
      <v-col cols="2">
        <v-tooltip top>
        <template v-slot:activator="{ on }">
        <v-text-field label="Rounding amount" dense outlined 
          :prepend-inner-icon="summary.roundingType === 1 ? 'mdi-arrow-up' : 'mdi-arrow-down'" 
          @click:prepend-inner="summary.roundingType *= -1; calculateTotalValue()" v-on="on" 
          autocomplete="off" 
          v-on:keypress="$isNumber" 
          v-model="summary.roundingAmount"
          @input="calculateTotalValue"
          :disabled="summary.totalGrossAmount == 0"/>
        </template>
          <span v-show="summary.roundingType === 1"> Click <v-icon color="white"> mdi-arrow-up </v-icon> to Round Down </span>
          <span v-show="summary.roundingType < 0"> Click <v-icon color="white"> mdi-arrow-down </v-icon> to Round Up </span>
        </v-tooltip>
      </v-col>
     <v-col cols="1">
     </v-col>
      <v-col>
      <v-simple-table >
        <tbody>
          <tr>
            <td> Net Amount </td>
            <td class="text-right"> {{$formatMoney(summary.totalNetAmount)}} </td>
          </tr>
          <tr>
            <td> Total Discount </td>
            <td class="text-right"> {{$formatMoney(summary.totalDiscount)}} </td>
          </tr>
          <tr>
            <td> Total Tax </td>
            <td class="text-right" > {{$formatMoney(summary.totalTaxAmount)}} </td>
          </tr>
        </tbody>
        </v-simple-table>
      </v-col>
      <v-divider vertical class="mb-4"></v-divider>
      <v-col>
        <v-simple-table >
        <tbody>
          <tr>
            <td style="font-size: 0.99em"> Gross Amount </td>
            <td class="text-right" style="font-size: 1.1em"> {{$formatMoney(summary.totalGrossAmountRounded)}}  </td>
          </tr>
          <tr>
            <td style="font-size: 0.99em"> Pre - payment </td>
            <td class="text-right" style="font-size: 1.1em"> {{$formatMoney(summary.prePayment)}}  </td>
          </tr>
          <tr>
            <td style="font-size: 1.1em; font-weight: 500"> Balance </td>
            <td class="text-right" style="font-size: 1.25em; font-weight: 500"> {{$formatMoney(summary.paymentDue)}} </td>
          </tr>
        </tbody>
        </v-simple-table>
      </v-col>
    </v-row>-->
    <div style="display: flex; justify-content: space-between; padding: 1px 20px;">
      <span>
        <v-textarea label="Comments" rows="4" cols="50" outlined v-model="summary.comments"></v-textarea>
      </span>
      
      <div style="display: block">
        <span style="display: flex">
          <v-simple-table >
            <tbody>
              <tr>
                <td> Net Amount </td>
                <td class="text-right"> {{$formatMoney(summary.totalNetAmount)}} </td>
              </tr>
              <tr>
                <td> Total Discount </td>
                <td class="text-right"> {{$formatMoney(summary.totalDiscount)}} </td>
              </tr>
              <tr>
                <td> Total Tax </td>
                <td class="text-right" > {{$formatMoney(summary.totalTaxAmount)}} </td>
              </tr>
            </tbody>
          </v-simple-table>
          <v-divider vertical class="mb-4"></v-divider>
          <v-simple-table >
            <tbody>
              <tr>
                <td style="font-size: 0.99em"> Gross Amount </td>
                <td class="text-right" style="font-size: 1.1em"> {{$formatMoney(summary.totalGrossAmount)}}  </td>
              </tr>
              <tr>
                <td style="font-size: 0.99em"> Pre - payment </td>
                <td class="text-right" style="font-size: 1.1em"> {{$formatMoney(summary.prePayment)}}  </td>
              </tr>
              <tr>
                <td style="font-size: 0.99em"> Round Off </td>
                <td class="text-right" style="font-size: 1.1em"> {{summary.roundingAmount.toFixed(3)}}  </td>
              </tr>
            </tbody>
          </v-simple-table>
        </span>
        <span style="display: flex; justify-content: center; border-top: 2px solid grey; border-bottom: 2px solid grey; padding: 5px 1px; margin-top: 5px">
          <td style="font-size: 1.6em; color: gray" class="text-left mx-8"> Payment due </td>
          <v-text-field dense solo outlined v-model="summary.paymentDue" class="mb-n6" append-icon="mdi-content-save" v-if="editPaymentDue" @click:append="editPaymentDue = false" v-on:keypress="$isNumber" v-on:keyup.enter=" editPaymentDue = false " />
          <td style="font-size: 1.8em; " class="text-right" v-else > {{$formatMoney(summary.paymentDue)}} <v-icon color="success" large @click="editPaymentDue = true"> mdi-pencil-box </v-icon></td>
          
        </span>
      </div>
    </div>
  
    <v-row class="mx-3 pr-3 mt-3 pb-1">
      <v-spacer></v-spacer>
      <v-btn large color="primary" class="mx-2" @click="discard(false)">Discard</v-btn>
      <v-btn large color="primary" class="mx-2" :disabled="billingItems.length <= 0">Save as Draft</v-btn>
      <v-btn large color="primary" class="mx-2" @click="saveandpreview" :disabled="billingItems.length <= 0">Save & Preview</v-btn>
    </v-row>

    <v-dialog v-model="discarddialog"  max-width="250">
      <v-card>
        <v-card-title class="headline">Discard</v-card-title>
        <v-card-text>Are you sure you want to discard this transaction? </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="discarddialog = false;  discard(true)" >  YES  </v-btn>
          <v-btn color="green darken-1" text @click="discarddialog = false" >  NO     </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <ChooseContactDetails ref="changecontactdetailsdialog" v-on:onClose="newcontactdetails" />
    <ChooseAddressDetails ref="changeaddressdetailsdialog" v-on:onClose="newaddressdetails" />

  </v-container>
</template>

<script>
import ReferanceDialog from '@/components/ReferanceDialog.vue'
import DataLoadSpinner from '@/components/DataLoadSpinner.vue'
import ChooseContactDetails from '@/components/ChooseContactDetails.vue'
import ChooseAddressDetails from '@/components/ChooseAddressDetails.vue'
import BillingItemEntryForm from '@/components/BillingItemEntryForm.vue'

  export default {
    name: 'CreateTaxInvoice',
    components: {
      ReferanceDialog,
      DataLoadSpinner,
      ChooseContactDetails,
      ChooseAddressDetails,
      BillingItemEntryForm
    },
    watch:{
        'summary.paymentDue': function(newval)
        {
            this.summary.paymentDue = newval === '' ? 0 : parseFloat(newval, 10);
            this.summary.roundingAmount =  ( newval === '' ? 0 : parseFloat(newval, 10) ) - (this.summary.totalGrossAmount - this.summary.prePayment);
        }
     },
    computed:{
        termslist(){
            return this.$store.getters.getAvailableTerms;
        },
        paymenttypelist(){
            return this.$store.getters.getAvailablePaymentTypes;
        }
    },
    data : () => ({
        addressdialog: false,
        contactdialog: false,
        discarddialog: false,
        editBillItem: false,
        editPaymentDue: false,
        invdt: false,
        duedt: false,
        editbillitemindex: -1,

        partneraddress:[],
        partnercontact:[],
        creditmemolist: [],
        delvchalnlist:[],

        headervalues:{
          outstandingBalance:0,
          partnername: '',
          partnertaxnum:'',
          creditmemo:'',
          delvchaln: '',
          contactdetails:{},
          address:{},
          terms: '',
          paymenttypes: [],
          invoicedate: new Date().toISOString().substring(0,10),
          invoiceduedate: ''         
        },
        billingItems:[],
        summary: {
            comments: '',
            roundingAmount: 0,
            totalNetAmount: 0,
            totalDiscount: 0,
            totalTaxAmount : 0,
            totalGrossAmount: 0,           
            totalGrossAmountRounded: 0,
            prePayment: 0,
            paymentDue:0 
        }
    }),
    methods: {
      changecontactdetails: function(){ 
          this.$refs.changecontactdetailsdialog.show(this.partnercontact, this.headervalues.partnername)   
      },
      newcontactdetails: function(data){
          this.headervalues.contactdetails = data;
      },
      changeaddressdetails: function(){ 
          this.$refs.changeaddressdetailsdialog.show(this.partneraddress, this.headervalues.partnername)   
      },
      newaddressdetails: function(data){ 
          this.headervalues.address = data; 
      },
      insertNewItem: function(data){
          this.billingItems.push(data);
          this.calculateTotalValue();
          this.scrollToEnd();
      },
      saveChangedBillItem: function(itemdata)
      {
          this.billingItems[this.editbillitemindex].discountinpercent = itemdata.discountinpercent;
          this.billingItems[this.editbillitemindex].productDesc = itemdata.productDesc;
          this.billingItems[this.editbillitemindex].qnty = itemdata.qnty;
          this.billingItems[this.editbillitemindex].unit = itemdata.unit;
          this.billingItems[this.editbillitemindex].rate = itemdata.rate;
          this.billingItems[this.editbillitemindex].discount.percentage = itemdata.discount.percentage;
          this.billingItems[this.editbillitemindex].discount.numericvalue = itemdata.discount.numericvalue;
          this.billingItems[this.editbillitemindex].taxApplicable = itemdata.taxApplicable;
          this.billingItems[this.editbillitemindex].taxValue = itemdata.taxValue;
          this.billingItems[this.editbillitemindex].totalTaxPercentage = itemdata.totalTaxPercentage;     
          this.billingItems[this.editbillitemindex].totalTaxNumericValue = itemdata.totalTaxNumericValue;
          this.billingItems[this.editbillitemindex].grossAmount = itemdata.grossAmount;
          this.editBillItem = false;
          this.calculateTotalValue();
      },
      editBillItemFunc: function(index)
      {
          this.editBillItem = true;
          this.editbillitemindex = index;
          this.$refs.edititemdialog.editItem(this.billingItems[index])
      },
      deleteItem: function(index){
        this.billingItems.splice(index,1);
        this.calculateTotalValue()
      },
      discard: function(value)
      {
        if(value == true)
        {
            this.partneraddress = [];
            this.partnercontact = [];
            this.creditmemolist = [];
            this.delvchalnlist = [];
            this.billingItems = [];

            this.headervalues = {
              outstandingBalance:0,
              partnername: '',
              partnertaxnum:'',
              creditmemo:'',
              delvchaln: '',
              contactdetails:{},
              address:{},
              terms: '',
              paymenttypes: [],
              invoicedate: new Date().toISOString().substring(0,10),
              invoiceduedate: ''         
            };
            this.changepartner()
        }
        else
          this.discarddialog = true;
      },

      calculateTotalValue()
      {
          this.summary.totalNetAmount =  this.billingItems.map(x => x.qnty * x.rate).reduce((a,b)=> a + b);
          this.summary.totalDiscount = this.billingItems.map(x => x.discount.numericvalue).reduce((a,b)=> a + b);
          this.summary.totalTaxAmount = this.billingItems.map(x => x.totalTaxNumericValue).reduce((a,b)=> a + b);
          this.summary.totalGrossAmount = this.billingItems.map(x => x.grossAmount).reduce((a,b)=> a + b);   

          this.summary.paymentDue = this.summary.totalGrossAmount - this.summary.prePayment;

          
      },

      scrollToEnd: function(){
        this.$nextTick( () =>{ document.documentElement.scrollTop = document.documentElement.scrollHeight});
      },

      saveandpreview(){
        this.$router.push({name: 'InvoicePreview',  query: { "inv": 'TINV-A123' } })
      },

      loaddata(data)
      {
        this.$refs.getPartnerInfo.close()
        this.$refs.spinner.show();
        setTimeout(()=>{
          this.$refs.spinner.close();
            this.headervalues.partnername = data.name,
            this.outstandingBalance = 12345678.91;
            this.headervalues.contactdetails = {id: 1, name:'Girish',phone:'+49-1513437649462',email:'girish.12243@gmail.com'},
            this.headervalues.address = {addressline:'Menzinger Str 21', postcode: '80638', city: 'Munich', state:'Bavaria', country: 'Germany' },
            this.headervalues.partnertaxnum = "AGSJY7-HJAH2-GHSYT-21SGD"
            this.partneraddress.push({addressline:'Leopardstrasse 43', postcode: '90845', city: 'Munich', state:'Bavaria', country: 'Germany' });
            this.partneraddress.push({addressline:'354 Vijayanagar', postcode: '560077', city: 'Bengaluru', state:'Karnataka', country: 'INDIA' });
            this.partneraddress.push({addressline:'Leopardstr 31', postcode: '80453', city: 'Berlin', state:'Brandenburg', country: 'Germany' });
            this.partnercontact.push({id: 1, name:'Girish',phone:'+49-153648463533',email:'girish.14354@gmail.com'});
            this.partnercontact.push({id: 2, name:'Mahesh',phone:'+49-16123672234',email:'girish.24364@gmail.com'});
            this.partnercontact.push({id: 3, name:'Girish BN',phone:'+91-99454382222',email:'connect_girish@gmail.com'});
            this.summary.prePayment = 100;
        }, 500)
      },
      changepartner(){
        this.$refs.getPartnerInfo.show('Customer','Tax Invoice');
      }
    },
    mounted (){
      this.$refs.getPartnerInfo.show('Customer','Tax Invoice');
    }
  }
</script>

<style scoped>
#newvoucher{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
}

.voucherheadlinetext{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.voucherheadlinetext > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

</style>