<template>
<v-container id="salessummary" class="summary container--fluid">
    <div class="summaryheader">
        <span class="display-2 mt-1"> Delivery Challan </span>
        <div>
            <v-text-field class="mr-4" append-icon="mdi-magnify" placeholder="Search" v-model="searchtext"/>
            <span class="body-2 mt-3 mr-2" >
                {{searchperiod.from}} - {{searchperiod.to}}
                <v-icon class="mb-2 mr-3 selectioncalendar" @click="calendarselection = true">mdi-calendar</v-icon>
            </span>
            <v-btn x-large tile color="success" class="mr-5" @click="$router.push({name: 'CreateDeliveryChallan' })"> <v-icon> mdi-plus</v-icon> CREATE Delivery Challan </v-btn>
        </div>
    </div>
    <v-divider class="mt-1 mb-2"/>
    <div class="metricsbar">
        <div :class="selectedMetricIndex === index ? 'metrics metricsselected': 'metrics'" v-for="(item, index) in dcmetrics" :key="index" @click="selectedMetricIndex = selectedMetricIndex === index ? -1 : index"> 
            <span class="keyvalue"> {{item.value}} </span> 
            <span class="keytype"> {{item.key}} </span>
        </div>
    </div>
 <v-layout justify-center align-center class="mt-4">
  <v-flex xs12>
	<v-data-iterator 
		:items="delvchallans"
        :options="options"
		column
        :footer-props="{
          'items-per-page-options': [8, 15, 30, 40, 50]
        }"
        :search="searchtext"
		no-data-text="No Delivery Challan created yet">
        <v-flex slot="item" slot-scope="props" >
            
            <div class="px-2 dc-row"  @dblclick="gotoinvoicepreview(props.item.challannumber)">
                <span class="dc-row-components-25">
                <ul style="list-style-type:none; padding:0" class="mx-1">
                    <li class="subtitle-1 font-weight-medium">{{ props.item.partnername }} </li>
                    <li class="caption">{{ props.item.address }}</li>
                </ul>
                </span>

                 <span class="dc-row-components-25">
                <ul style="list-style-type:none; padding:0" class="mx-1">
                    <li class="body-2"><v-icon small class="mr-2">mdi-phone</v-icon>{{ props.item.phone }} </li>
                    <li class="body-2"><v-icon small class="mr-2">mdi-mail</v-icon>{{ props.item.email }}</li>
                </ul>
                 </span>

                <span class="dc-row-components-20">
                <ul style="list-style-type:none; padding:0" class="mx-1">
                    <li class="body-2"><v-icon small class="mr-2">mdi-file-document</v-icon>{{ props.item.challannumber }} </li>
                    <li class="body-2"><v-icon small class="mr-2">mdi-calendar</v-icon>{{ props.item.challandt }} </li>
                </ul>
                </span>

                <span class="dc-row-components-20">
                <tbody class="mx-1">
                    <tr><td class="body-2">Est. Cost</td><td class="body-1 font-weight-medium pl-2">: {{ $formatMoney(props.item.estcost) }} </td></tr>
                    <tr><td class="body-2">Status</td>   <td class="body-1 font-weight-medium pl-2">: <v-chip label small>{{ delvChalnList[props.item.status] }} </v-chip></td></tr>
                </tbody>
                </span>

                <span class="dc-row-components-10 justify-end">
                <ul style="list-style-type:none; padding:0" class="mx-1">
                    <li> <v-btn small tile block class="my-1" color="info" @click="createinvoice()"> Create Invoice </v-btn> </li>
                    <li> <v-btn small tile block class="my-1" color="info" @click="changedelvstatus = true; changedelvstatusfor.challanNumber =  props.item.challannumber; changedelvstatusfor.currentStatus = props.item.status"> Delv. Status </v-btn> </li>
                </ul>
                </span>
            </div>
            
        </v-flex>
	</v-data-iterator>
  </v-flex>
 </v-layout>

        <v-dialog ref="dialog" v-model="calendarselection" width="598px">
          <v-layout justify-center align-center>
            <v-flex row>
            <v-date-picker v-model="tmpdatefltr.from" no-title class="mx-1" ></v-date-picker>        
            <v-date-picker v-model="tmpdatefltr.to" no-title class="mx-1">
              <v-spacer></v-spacer>
              <v-btn text color="primary" @click="calendarselection = false">Cancel</v-btn>
              <v-btn text color="primary" @click="searchperiod = tmpdatefltr; calendarselection = false">OK</v-btn>
            </v-date-picker>
            </v-flex>
          </v-layout>
        </v-dialog>

        <v-dialog ref="dialog" v-model="changedelvstatus" width="750px">
            <v-card >
                <div style="background-color:#0AB2BF; padding-left: 20px; padding-top: 6px; padding-bottom: 6px; font-size: 1.2em; font-weight: 500; color: white; margin-bottom: 20px">
                    <span> Change Deliver Status: {{changedelvstatusfor.challanNumber}} </span>
                </div>
                <span style="margin-left: 100px"> Select the correct status </span>
               <v-layout justify-center align-center column>
                  <v-flex class="mt-1">           
                        <v-chip label class="mx-1" :disabled="changedelvstatusfor.currentStatus >= 1" @click="changedelvstatusfor.changedStatus = 1" :color="changedelvstatusfor.changedStatus === 1 ? 'info' : ''"> Shipped </v-chip>
                        <v-chip label class="mx-1" :disabled="changedelvstatusfor.currentStatus >= 2" @click="changedelvstatusfor.changedStatus = 2" :color="changedelvstatusfor.changedStatus === 2 ? 'info' : ''"> Delv. Approved </v-chip>
                        <v-chip label class="mx-1" :disabled="changedelvstatusfor.currentStatus >= 3" @click="changedelvstatusfor.changedStatus = 3" :color="changedelvstatusfor.changedStatus === 3 ? 'info' : ''"> Invoiced </v-chip>
                        <v-chip label class="mx-1" :disabled="changedelvstatusfor.currentStatus >= 4" @click="changedelvstatusfor.changedStatus = 4" :color="changedelvstatusfor.changedStatus === 4 ? 'info' : ''"> Returned for Correction </v-chip>
                        <v-chip label class="mx-1" :disabled="changedelvstatusfor.currentStatus >= 5" @click="changedelvstatusfor.changedStatus = 5" :color="changedelvstatusfor.changedStatus === 5 ? 'info' : ''"> Rejected </v-chip>
                  </v-flex>
                  <v-flex row class="mt-10" v-show="changedelvstatusfor.changedStatus === 1">
                   <v-menu v-model="shipddt" offset-y min-width="200px">
                      <template v-slot:activator="{ on }" >
                        <v-text-field  dense v-model="changedelvstatusfor.shippedOn" label="Shipped On"  outlined append-icon="mdi-calendar"  @click:append="shipddt = true" readonly v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="changedelvstatusfor.shippedOn" no-title @input="shipddt = false" scrollable ></v-date-picker>
                    </v-menu>
                  </v-flex>
                  <v-flex row class="mt-10" v-show="changedelvstatusfor.changedStatus === 2">
                   <v-menu v-model="rcvdddt" offset-y min-width="200px">
                      <template v-slot:activator="{ on }" >
                        <v-text-field  dense v-model="changedelvstatusfor.receivedOn" label="Received On"  outlined append-icon="mdi-calendar"  @click:append="rcvdddt = true" readonly v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="changedelvstatusfor.receivedOn" no-title @input="rcvdddt = false" scrollable ></v-date-picker>
                    </v-menu>
                   <v-text-field dense outlined class="mx-1" label="Received By"  v-model="changedelvstatusfor.receivedBy" /> 
                  </v-flex>
                  <v-flex row class="mt-10" v-show="changedelvstatusfor.changedStatus === 3">
                   <v-menu v-model="invcddt" offset-y min-width="200px">
                      <template v-slot:activator="{ on }" >
                        <v-text-field  dense v-model="changedelvstatusfor.invoicedOn" label="Invoiced On"  outlined append-icon="mdi-calendar"  @click:append="invcddt = true" readonly v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="changedelvstatusfor.invoicedOn" no-title @input="invcddt = false" scrollable ></v-date-picker>
                    </v-menu>
                   <v-text-field dense outlined class="mx-1" label="Invoice Number"  v-model="changedelvstatusfor.invoiceNumber" />
                  </v-flex>
                  <v-flex row class="mt-10" v-show="changedelvstatusfor.changedStatus === 4">
                   <v-textarea dense outlined class="mx-1" label="Reason for correction/Redo" cols="50" rows="3"  v-model="changedelvstatusfor.returnReason" />
                  </v-flex>
                  <v-flex row class="mt-10" v-show="changedelvstatusfor.changedStatus === 5">
                   <v-textarea dense outlined class="mx-1" label="Rejection Reason" cols="50" rows="3"  v-model="changedelvstatusfor.rejectReason" />
                  </v-flex>
                  <v-flex xs10 class="mb-5 mt-5">
                   <v-btn tile color="success" @click="changedelvstatus = false; changestatus()" :disabled="changedelvstatusfor.changedStatus===-1">submit </v-btn>
                  </v-flex>
               </v-layout>
              
            </v-card>
        </v-dialog>


 </v-container>
</template>
<script>
  export default {
    name: 'SalesSummary',
    computed:{
        delvChalnList() { return this.$store.getters.getDelvChallanStatusList }
    },
    data: () => ({
     options:{
        itemsPerPage: 8,
     },
     invcddt: false,
     shipddt: false,
     rcvdddt: false,
     selectedMetricIndex: -1,
     tmpdatefltr:{
         from: null,
         to: null
     },
     searchtext: '',
     searchperiod:{
         from: "2019-04-01",
         to: "2020-04-01"
     },
     calendarselection: false,
     changedelvstatus: false,
     changedelvstatusfor:{
         challanNumber : null,
         currentStatus: -1,
         changedStatus: -1,
         receivedBy: null,
         receivedOn: null,
         shippedOn: null,
         invoiceNumber: null,
         invoicedOn: null,
         returnReason: null,
         rejectReason: null
     },
     dcmetrics:[
        {
            key: "#Delv. Challan",
            value: 132,
            id: 0,
        },
        {
            key: "#Shipped",
            value: 30,
            id: 1,
        },
        {
            key: "#Delivered",
            value: 52,
            id: 2,
        },
        {
            key: "#Invoiced",
            value: 45,
            id: 3,
        },
        {
            key: "#Returned",
            value: 32,
            id: 4,
        },
        {
            key: "#Rejected",
            value: 0,
            id: 5,
        }],
     delvchallans:[{
       partnername: "G-Tech Global Solutions Technologies",
       address: "560091, Bengaluru, India",
       phone: "+91 - 99453454642",
       email:"girish.12345@gmail.com",
       challandt:"20-04-2019",
       challannumber: "DC-A0113452",
       estcost: 2345.32,       
       status: "0"
     },
     {
       partnername: "Dhavalagiri Enterprises",
       address: "80638, Munich, Germany",
       phone: "+49 - 16123435633",
       email:"girish.bangalore-nagarajaiah@outlook.com",
       challandt:"14-03-2020",
       challannumber: "DC-M0154678",
       estcost: 543689.32,       
       status: "1"
     }]
    }),
    methods: {
        changestatus(){
            this.changedelvstatusfor = {
                                         challanNumber : null,
                                         currentStatus: -1,
                                         changedStatus: -1,
                                         receivedBy: null,
                                         receivedOn: null,
                                         shippedOn: null,
                                         invoiceNumber: null,
                                         invoicedOn: null,
                                         returnReason: null,
                                         rejectReason: null
                                     }
        },
        gotoinvoicepreview(id)
        {
            this.$router.push({name: 'DeliveryChallanPreview', query: { "challan": id }})
        }
    },
    
  }
</script>

<style scoped>

.summary{
    min-height: 100%
}

.summaryheader{
    display: flex; 
    justify-content: space-between
}

.summaryheader > div{
    display: flex; 
}

.selectioncalendar:hover{
    color: blue;
    cursor: pointer;
}

.metricsbar{
    display: flex;
}

.metrics{
    border: 1px solid #0AB2BF; 
    display: flex; 
    flex-direction: column; 
    justify-content: center;
    text-align: center;
    padding: 2px 15px;
    background-color: #0AB2BF;
    color: white;
    flex-basis: 100%;
    margin: 2px 5px;
    user-select: none;
    cursor: pointer;
    border-radius: 3px;
}

.metricsselected{
    color: #03178C;
    font-weight: 500;
    background-color: rgb(29, 203, 216);
}

.metrics .keyvalue{
    font-size: 2em;
}

.metrics .keytype{
    font-size: 0.9em;
}

.dc-row{
    border:1px solid #D9D9D9; 
    display:flex; 
    justify-content:space-between; 
    user-select:none;
    border-radius: 3px;
    background-color: white;
    margin-top: 6px;
}

.dc-row:hover{
    background-color: #D9D9D9;
    cursor: pointer;
}

.dc-row-components-25{
    display: flex; 
    align-items: center; 
    max-width: 25%; 
    min-width: 25%;
    padding: 5px 2px;
}

.dc-row-components-20{
    display: flex; 
    align-items: center; 
    max-width: 20%; 
    min-width: 20%;
    padding: 5px 2px;
    justify-content: center;
}

.dc-row-components-10{
    display: flex; 
    align-items: center; 
    max-width: 10%; 
    min-width: 10%;
}

.metrics{
        display: flex;
    }


@media only screen and (max-width: 1100px) {

    .summaryheader{
        flex-direction: column;
    }

    .summaryheader > div{
        display: flex; 
        flex-direction: column;
    }


    .metricsbar{
        display: column;
        display: none;
    }

  

    .dc-row{
        flex-direction: column;
        max-width: 100%;
    }

    .dc-row-components-25, .dc-row-components-20, .dc-row-components-10{
        max-width: 100%;
        max-width: 100%;
        justify-content: flex-start;
        margin: 4px 2px;
    }

}

</style>