<template>
  <v-row justify="center" >
    <v-dialog v-model="displayDialog" persistent max-width="650"  >
      <v-card shaped>
        <v-card-title style="font-size: 1.2em; background-color: #0AB2BF; color: white" color="red" >Select your {{patnerType}} to generate {{creationType}}</v-card-title>
        <v-divider />
        <v-layout justify-center align-center class="mt-3">
          <v-flex xs8>
            <v-text-field v-model="searchtext" :label="'Search '+patnerType" class="font-weight-medium" append-icon="mdi-magnify" autofocus></v-text-field>
          </v-flex>
        </v-layout>
        <v-layout justify-center align-center>           
        <span class="partnersearchlist" style="width: 70%;">
          
          <ul  v-for="(item, index) in filteredItems" :key="index" style="list-style-type:none; padding:0;">
                <li>
                  <div 
                    v-ripple :class="item.id === selectedpatnerid ? 'pl-2 pt-2 selected' : 'pl-2 pt-2' " 
                    style="border-bottom: 1px solid #BFBFBF; display: flex; flex-direction: column"
                    @click="selectedpatnerid = item.id; patnerindex = index">
                    <span class="subtitle-1 font-weight-medium">{{item.name}}</span>
                    <span class="body-2 mt-n1" style="color: grey">Bengaluru, Karnataka, India</span>
                  </div>
                </li>
          </ul>
        </span>
        </v-layout>
        <v-layout justify-center align-center v-show="patnerindex === null"> 
          <span class="partnersearchlist" style="width: 70%;">
            <v-btn color="success" block tile > New {{patnerType}}</v-btn>
          </span>
        </v-layout>
        <v-layout justify-center align-center v-if="patnerindex != null" class="mt-1"> 
          <span style="width: 70%;display: flex; border: 1px solid #BFBFBF; background-color:#C2FFDC ">
          <v-icon color="success" class="mx-2"> mdi-check-circle </v-icon>
            <div 
              class="pl-2 pt-2" 
              style="width: 80%; display: flex; flex-direction: column"
              >
                    <span class="subtitle-1 font-weight-medium">{{patners[patnerindex].name}}</span>
                    <span class="body-2 mt-n1" style="color: grey">Bengaluru, Karnataka, India</span>
                  </div>
          <v-icon color="grey" class="mx-2" @click="selectedpatnerid = null; patnerindex = null">mdi-close </v-icon>
          </span>
           
        </v-layout>
        
        <v-layout justify-end align-center class="pt-12 pb-4 mr-8">  
            <div style="width: 35%"> 
              <v-btn color="info" block @click="cancel()" >Cancel</v-btn>
            </div>
            <div style="width: 35%; margin-left: 20px"> 
              <v-btn color="info" block @click="sendData()" :disabled="selectedpatnerid === null" >Next</v-btn>
            </div>
        </v-layout>

      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    data () {
      return {
        searchtext: '',
        selectedpatnerid: null,
        patnerindex: null,
        displayDialog: false,
        patnerType: 'Customer',
        creationType: 'Delivery Challan',
        referanceId: null,
        patners:[{id: 1,  name: "Girish" }, 
                {id: 2,  name: "Dhavalagiri Enterprises LLP" }, 
                {id: 3,  name: "Mahesh" }, 
                {id: 4,  name: "Satish" }, 
                {id: 5,  name: "Giorgio" }, 
                {id: 6,  name: "Bozo" }, 
                {id: 7,  name: "Durim" },  
                {id: 8,  name: "Jagadeesh" }]
      }},
      methods: {
        cancel(){
          this.patnerid = null;
          this.displayDialog = false;
          this.$router.go(-1)
        },
        show(patnerType, creationType){
          this.displayDialog = true;
          if(patnerType)
            this.patnerType = patnerType;
          if(creationType)
            this.creationType = creationType;
        },
        close(){
          this.displayDialog = false;
        },
        sendData() {
          this.$emit('onClose', this.patners[this.patnerindex])
        }
    },
    computed: {
      filteredItems() { return this.patners.filter( x => x.name.toLowerCase().includes(this.searchtext.toLowerCase())) }
    }
  }
</script>

<style scoped>

span{
  user-select:none;
}

.selected{
  background-color: #BFBFBF;
  
}

.partnersearchlist{
  max-height: 320px;
  overflow-y: scroll;
}

.partnersearchlist::-webkit-scrollbar {
  width: 3px;
}
 
.partnersearchlist::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
}
 
.partnersearchlist::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

</style>