<template>
  <v-container id="Configuration" class="container--fluid">
    <div class="ConfigurationHeaders">
        <span style="display: flex"> UoM Configuration  </span>
    </div>
    
    <span>
        <h1 class="headline ml-12 mt-5 font-weight-medium">Select the "Units of Measure"(UoM) applicable to your company</h1>
    </span>
    <div class="form">
      <span v-for="(item, index) in availableUoM" :key="index">
        <div class="row">
            <span :class="item.isSelected ? 'title optionName optionSelected' : 'title optionName' " > {{item.formalName}} [ {{item.shortName}} ]</span> 
            <span v-if="editindex != index" style="display: flex">
            <span> <v-checkbox v-model="item.isSelected"  label="Is Applicable"  color="success" class="mt-n0"/> </span>
            <span> <v-btn x-small class="mt-5 ml-5" color="success" @click="editindex = index"> Edit Name </v-btn> </span>
            </span>
            <span v-else  class="editexistingunit">
            <span> <v-text-field  v-model="newFormalName" dense label="Formal Name" outlined class="mx-1 mt-3" :error="formalNameError" autocomplete="off"/> </span>
            <span> <v-text-field  v-model="newShortName" dense label="Short Name"  outlined class="mx-1 mt-3" maxlength="8" :error="shortNameError" autocomplete="off" @keydown.enter.prevent="SaveEdit"/> </span>
            <span> <v-btn class="mx-2 mt-3" color="success" @click="SaveEdit"> Save </v-btn> 
            <v-btn class="mt-3" color="success" @click="CancelEdit"> Cancel </v-btn> </span>
            </span>
        </div>
      </span>
        <div class="row justify-center mt-12 pb-8">
          <v-btn tile color="success" class="mx-2 my-2" :disabled='editindex != -1' @click="addNewUoMType"> <v-icon> mdi-plus</v-icon> Create New Unit Type </v-btn>
          <v-btn tile color="success" class="mx-2 my-2"> Save  </v-btn>
        </div>

        


    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'UoMConfig',
    watch:{
        'newFormalName': function(newval)
        {
            if(newval.trim() != '')
                this.formalNameError = false
        },
        'newShortName': function(newval)
        {
            if(newval.trim() != '')
                this.shortNameError = false
        }
    },
    computed:{
    },
    data: () => ({
        selected: true,
        editindex: -1,
        newFormalName: '',
        newShortName:'',
        formalNameError: false,
        shortNameError: false,
        availableUoM: [ {shortName: 'BOX', formalName: 'Carton Box', isSelected: false}, {shortName: 'PALLET', formalName: 'Pallet', isSelected: false},
                        {shortName: 'PKT', formalName: 'Packet', isSelected: false}, {shortName: 'PCS', formalName: 'Piece', isSelected: false},
                        {shortName: 'NOS', formalName: 'Number', isSelected: false}, {shortName: 'BAG', formalName: 'Bag', isSelected: false},
                        {shortName: 'PAIR', formalName: 'Pair', isSelected: false}, {shortName: 'BUNDLE', formalName: 'Bundle', isSelected: false},
                        {shortName: 'ROLL', formalName: 'Roll', isSelected: false}, {shortName: 'SET', formalName: 'Set', isSelected: false},

                        {shortName: 'MG', formalName: 'Milli Gram', isSelected: false}, {shortName: 'GRAM', formalName: 'Gram', isSelected: false},
                        {shortName: 'KG', formalName: 'Kilo Gram', isSelected: false}, {shortName: 'QT', formalName: 'Quintal', isSelected: false},
                        {shortName: 'TON', formalName: 'Metric Ton', isSelected: false},

                        {shortName: 'ML', formalName: 'Milli Liter', isSelected: false}, {shortName: 'L', formalName: 'Liter', isSelected: false},
                        {shortName: 'BBL', formalName: 'Barrel', isSelected: false}, {shortName: 'GL', formalName: 'Gallon', isSelected: false},

                        {shortName: 'INCH', formalName: 'Inches', isSelected: false}, {shortName: 'SQ.INCH', formalName: 'Square Inches', isSelected: false},
                        {shortName: 'CMTR', formalName: 'Centimeter', isSelected: false}, {shortName: 'SQ.CMTR', formalName: 'Square Centimeter', isSelected: false},
                        {shortName: 'MTR', formalName: 'Meter', isSelected: false}, {shortName: 'SQ.MTR', formalName: 'Square Meter', isSelected: false},
                        {shortName: 'FEET', formalName: 'Feet', isSelected: false}, {shortName: 'SQ.FEET', formalName: 'Square Feet', isSelected: false}],
    }),
    methods: {
        SaveEdit(){
            if(this.newFormalName.trim() === '')
               this.formalNameError = true;
            else if(this.newShortName.trim() === '')
               this.shortNameError = true;
            else
            {
              this.availableUoM[this.editindex].shortName = this.newShortName.trim();
              this.availableUoM[this.editindex].formalName = this.newFormalName.trim();
              this.CancelEdit();
            }
        },
        CancelEdit(){
            if(Object.keys(this.availableUoM[this.editindex]).length === 0)
                this.availableUoM.splice(this.editindex, 1)
            this.newShortName = '';
            this.newFormalName = '';
            this.shortNameError = false;
            this.formalNameError = false;
            this.editindex = -1
        },
        addNewUoMType(){
            this.availableUoM.push({});
            this.editindex = this.availableUoM.length - 1;
        }
    }
    
}
</script>

<style scoped>
#Configuration{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.ConfigurationHeaders{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.ConfigurationHeaders > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    display: flex; 
    margin-top: 10px;
}

.optionName{
    padding-top:15px; 
    min-width: 300px; 
    color: grey;
}

.optionSelected{
    color: #202020;
    font-weight: bold;
}

.editexistingunit{
    display: flex;
}


@media only screen and (max-width: 1000px) {


    .row{
        display: flex;
        flex-direction: column;
    }
    .editexistingunit{
        display: flex;
        flex-direction: column;
    }
}


</style>