<template>
  <v-row justify="center" >
    <v-dialog v-model="displayDialog" persistent width="880px" >
      <v-card >
        <v-card-title style="font-size: 1.2em; background-color: #0AB2BF; color: white" color="red" >Create New Business Partner</v-card-title>
        <v-divider />
        <v-layout justify-center align-center column >
  
            <div class="row">
                <span class="row-small-label"><v-label> Organization Name </v-label> </span>
                <span class="row-small-text"><v-text-field v-model="bp.name" autocomplete="off"></v-text-field></span>
            </div>

            <div class="row">
                <span class="row-small-label"><v-label> Partner Type </v-label> </span>
                <span style="display: flex; justify-content: space-around">
                   <v-checkbox label="Vendor" class="mx-3" v-model="bp.isVendor"/>
                   <v-checkbox label="Customer" class="mx-3" v-model="bp.isCustomer"/>
                </span>
                <span class="row-small-text"><v-select label="Currency" :items="['Indian Rupee - INR(₹)']" class="mx-12" v-model="bp.currency" autocomplete="off"/></span>
            </div>

            <span v-for="(itmC, idxcnt) in bp.contact" :key="idxcnt+'cnt'" class="rowparent">
              <div class="row">
                  <span class="row-small-text"><v-text-field v-model="itmC.name" label="Contact Person Name" autocomplete="off"/></span>
                  <span class="row-small-text" />
              </div>
              <div class="row">
                  <span class="row-small-text mt-n4"><v-text-field v-model="itmC.phone" label="Phone Number" autocomplete="off"/></span>
                  <span class="row-small-text mt-n4"><v-text-field v-model="itmC.email" label="Email" autocomplete="off"/></span>
              </div>
            </span>
            <div class="mt-n3">
                <v-btn tile x-small color="#D93D04" dark class="mx-2" @click="RemoveAnotherContact" v-show="bp.contact.length > 1"> Remove Contact</v-btn>
                <v-btn tile x-small color="#3C9DAB" dark @click="AddAnotherContact"> Add Another Contact </v-btn>
            </div>

            <span v-for="(itmA, idxaddr) in bp.address" :key="idxaddr+'addr'" class="rowparent">
               <div class="row">
                   <span class="row-small-text"><v-select :items="availablecountries" v-model="itmA.country" label="Country" autocomplete="off"/></span>
                   <span class="row-small-text"><v-select :items="getavailableState(itmA.country)" v-model="itmA.state" label="State" autocomplete="off"/></span>
               </div>
               <div class="row mt-n4">
                   <span class="row-small-text"><v-text-field v-model="itmA.pincode" class="mx-2" label="Pin Code" autocomplete="off"/></span>
                   <span class="row-small-text"><v-text-field v-model="itmA.city" class="mx-2" label="City" autocomplete="off"/></span>
               </div>
               <div class="row mt-n4">
                   <span class="row-large-text"><v-text-field v-model="itmA.address" label="Address Line" autocomplete="off"/></span>
               </div>
            </span>
            <div class="mt-n3">
                <v-btn tile x-small color="#D93D04" dark class="mx-2" @click="RemoveAnotherAddress" v-show="bp.address.length > 1"> Remove Address</v-btn>
                <v-btn tile x-small color="#3C9DAB" dark class="mx-2" @click="AddAnotherAddress"> Add Another Address </v-btn>
            </div>
          </v-layout>
        
        <v-layout justify-end align-center class="pt-12 pb-4 mr-8">  
            <div style="width: 35%"> 
              <v-btn color="info" tile block @click="close()" >Cancel</v-btn>
            </div>
            <div style="width: 35%; margin-left: 20px"> 
              <v-btn color="info" tile block @click="sendData()" >Save</v-btn>
            </div>
        </v-layout>

      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    name: 'BusinessPartnerCreationDialog',
    props:{
        type:[]
    },
    components:{},
    computed:{
      availablecountries(){
            return this.$store.getters.getCountries
        },
        availablecurrency(){
            return this.$store.getters.getCountries
        }
    },
    data () {
      return {
          displayDialog: false,
          bp:{
              name: '',
              isVendor: false,
              isCustomer: false,
              currency: '',
              contact: [{
                name: '',
                phone: '',
                email: '',
              }],
              address:[{
                  country: '',
                  state: '',
                  pincode: '',
                  city: '',
                  address: ''
              }]
          }
      }},
      methods: {
            AddAnotherContact(){
                this.bp.contact.push({
                                  name: '',
                                  phone: '',
                                  email: '',
                                });
            },
            AddAnotherAddress(){
                this.bp.address.push({
                                  country: '',
                                  state: '',
                                  pincode: '',
                                  city: '',
                                  address: ''
                                });
            },
            RemoveAnotherContact(){
                this.bp.contact.pop()
            },
            RemoveAnotherAddress(){
                this.bp.address.pop()
            },
            getavailableState(input){
                return this.$store.getters.getState(input)
            },
            show(iptype){
              this.displayDialog = true;
              console.log(JSON.stringify(iptype))
              if(iptype.includes('VENDOR'))
                this.isVendor = true;
              if(iptype.includes('CUSTOMER'))
                this.isCustomer = true;
            },
            close(){
              this.displayDialog = false;
            },
            sendData() {
                this.close();
            }
    }   
  }
</script>

<style scoped>

.row{
    display: flex;
    margin-top: 10px;
}

.row-small-label{
  width: 150px;
  padding-top: 20px;
}

.row-small-text{  
    width: 400px;
    margin: 0px 4px;
}

.row-large-text{  
    width: 800px
}

@media only screen and (max-width: 875px) {

    .row{
        flex-direction: column;
        width: 90%;
    }

    .row-small-label, .row-small-text, .row-large-text{
      width: 100%;
      justify-content: center;
      align-content: center;
    }

    .rowparent{
        margin-left: 50px;
        width: 100%;
        justify-content: center;
        align-content: center;
    }


}
</style>