<template>
  <v-container id="Configuration" class="container--fluid">
    <div class="ConfigurationHeaders">
        <span style="display: flex"> Payment Configuration  </span>
    </div>
    
    <span>
       <h1 class="headline ml-12 mt-5 font-weight-medium">Create your Terms and Conditions for payments on Invoices.</h1>
    </span>
    <div class="form">

      <span v-for="(item, index) in terms" :key="index">
        <div :class="editTermIndex === index ? 'rowdisabled': 'row'">
          <span class="row-small-label headline font-weight-medium"> {{item.name}}</span>
          <span class="row-small-text">
            <ul style="list-style-type:none; padding:0">
                <li class="title"> {{ item.creditDays === 0 ? 'Requires Immediate Payment' : 'Payments are due by ' + item.creditDays + ' days' }}</li>
                <li class="subtitle-1 font-weight-medium font-italic" v-show="item.partialPayment"> Partial Payments {{item.partialPayment ? 'Allowed' : 'Not Allowed'}}</li>
                <li class="body-1 font-italic ml-3"> {{item.comments}}</li>
            </ul>
            <v-btn small tile color="info" class="mx-2" @click="editTerms(index)" :disabled="editTermIndex === index"> Edit </v-btn>
            <v-btn small tile color="error" class="mx-2" @click="removeTerms(index)" :disabled="terms.length <= 1 || editTermIndex === index"> Delete </v-btn>
            </span>
        </div>
      </span>


      <span v-show="addNewTerms">
        <div class="row" >
           <span class="row-small-label"><v-label> Condition Name </v-label></span>
           <span class="row-x-small-text"><v-text-field autofocus class="" dense solo outlined  maxlength="8" v-model="newTerms.name" placeholder="NET-365" :error="entryerror.name"/></span>
        </div>
        <div class="row mt-n1">
           <span class="row-small-label"><v-label> Number of Credit Days </v-label></span>
           <span class="row-x-small-text"><v-text-field class="" dense solo outlined v-on:keypress="$isNumber" v-model="newTerms.creditDays" placeholder="365" :error="entryerror.creditDays"/></span>
           <span class="row-appended-text"> Days </span>
        </div>
        <div class="row mt-n1">
           <span class="row-small-label"><v-label> Partial Payment </v-label></span>
           <span class="row-x-small-text mt-n3"><v-checkbox label="Accepted"  color="success" class="mt-n0" v-model="newTerms.partialPayment"/></span>
        </div>
        <div class="row mt-n1">
           <span class="row-small-label"><v-label> Terms and Conditions </v-label></span>
           <span class="row-small-text"><v-textarea outlined v-model="newTerms.comments" placeholder="Payment Terms and Conditions"/></span>
        </div> 
      </span>       

 
        <div class="row justify-center pb-8">
          <v-btn tile large color="success" class="mx-2 my-2" @click=" addNewTerms = true" v-show="!addNewTerms"> <v-icon> mdi-plus</v-icon> Create New Terms </v-btn>
          <v-btn tile class="mx-2 my-2" v-show="addNewTerms" @click="discardEditing()"> <v-icon> mdi-cancel </v-icon> Discard Editing</v-btn>
          <v-btn tile color="success" class="mx-2 my-2" @click="addNewTermsAndCondition" v-show="addNewTerms"> <v-icon> mdi-plus</v-icon> Save Terms and Condition </v-btn>
        </div>

    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'TermsandConditionsConfig',
    watch:{
        'newTerms.name': function(newVal){
            if(newVal.trim() !== '')
                this.entryerror.name = false
        },
        'newTerms.creditDays': function(newVal){
            if(newVal)
                this.entryerror.creditDays = false
        }
    },
    computed:{
    },
    data: () => ({
        addNewTerms: false,
        editTermIndex : -1,
        newTerms:
        {
           "name": '',
           "creditDays" : 0,
           "partialPayment" : false,
           "comments": ''
        },
        entryerror:{
            name: false,
            creditDays: false
        },
        terms: [
            {
               "name": "Immediate",
               "creditDays" : 0,
               "partialPayment" : false,
               "comments": "This invoice serves as proof of purchase. Payale Amount are subjected to be payed on or before due date, Late payment are subjected for late payment fee or interest charges"
            },
            {
               "name": "Net-7",
               "creditDays" : 7,
               "partialPayment" : false,
               "comments": "This invoice serves as proof of purchase. Payale Amount are subjected to be payed on or before due date, Late payment are subjected for late payment fee or interest charges"
            },
            {
               "name": "Net-30",
               "creditDays" : 30,
               "partialPayment" : false,
               "comments": "This invoice serves as proof of purchase. Payale Amount are subjected to be payed on or before due date, Late payment are subjected for late payment fee or interest charges"
           },
            {
               "name": "Net-90",
               "creditDays" : 90,
               "partialPayment" : true,
               "comments": "This invoice serves as proof of purchase. Payale Amount are subjected to be payed on or before due date, Late payment are subjected for late payment fee or interest charges"
           },
            {
               "name": "Net-180",
               "creditDays" : 180,
               "partialPayment" : true,
               "comments": "This invoice serves as proof of purchase. Payale Amount are subjected to be payed on or before due date, Late payment are subjected for late payment fee or interest charges"
           }
        ]
    }),
    methods: {
        addNewTermsAndCondition(){
            if(this.newTerms.name.trim().length <= 0 )
                this.entryerror.name = true;
            else if(isNaN(this.newTerms.creditDays) || this.newTerms.creditDays == '' ||this.newTerms.creditDays < 0)
                this.entryerror.creditDays = true;
            else
            {
               if(this.editTermIndex >= 0)
                   this.removeTerms(this.editTermIndex)
   
               this.terms.push(Object.assign({}, this.newTerms));
               this.addNewTerms = false;
               this.discardEditing();
            }

        },
        removeTerms(index)
        {
            this.terms.splice(index, 1);
        },
        editTerms(index)
        {
            this.editTermIndex = index;
            this.addNewTerms = true;
            this.newTerms.name = this.terms[index].name;
            this.newTerms.creditDays = this.terms[index].creditDays;
            this.newTerms.partialPayment = this.terms[index].partialPayment;
            this.newTerms.comments = this.terms[index].comments;
            this.$nextTick( () =>{ document.documentElement.scrollTop = document.documentElement.scrollHeight});
        },
        discardEditing()
        {
            this.editTermIndex = -1;
            this.addNewTerms = false;
            this.newTerms.name = '';
            this.newTerms.creditDays = '';
            this.newTerms.partialPayment = false;
            this.newTerms.comments = '';
        }
    }
    
}
</script>

<style scoped>
#Configuration{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.ConfigurationHeaders{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.ConfigurationHeaders > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    display: flex; 
    margin-top: 50px;
}

.rowdisabled{
    display: flex; 
    margin-top: 50px;
    color: gray;
}

.optionName{
    padding-top:15px; 
    min-width: 300px; 
    color: grey;
}

.row-small-label{
    width: 200px;
    margin-left: 40px;
    margin-top: 8px;
}

.row-x-small-text{
    width: 250px;
    margin-left: 40px;
}

.row-small-text{
    width: 40%;
    margin-left: 40px;
}

.row-appended-text{
    padding-top: 4px; 
    margin-left: 10px; 
    font-size: 1.2em; 
    font-weight:500; 
}



@media only screen and (max-width: 1000px) {

    .row{
        display: flex;
        flex-direction: column;
        padding-top: 20px;
    }

    .row-small-label{
        margin-top: -20px;
        margin-bottom: 5px;
        width: 100%;
        margin-left: 0px;
    }

    .row-small-text{
        width: 100%;
        margin-left: 0px;
    }

    .row-x-small-text{
        width: 100%;
        margin-left: 0px;
    }

    .row-appended-text{
        width: 100%;
        margin-top: -32px;
        padding-bottom: 20px;
        
    }


}


</style>