<template>
      <!-- Billing item details Entry Section Row 1-->
    <div style="display: flex; flex-direction:column; margin: 20px 5px;">
      <transition name="bounce">
      <div style="display: flex; justify-content: center" v-if="showitementrybar">
        <span style="display: flex; flex-direction: column; min-width: 300px" >
          <v-combobox :items="products" item-text="name" item-value="desc" 
            label="Product **" dense outlined v-model="product"
            ref="productselection"
            :error="formError.productname"
            :error-messages="formError.productname ? `Product cannot be empty` : ''"
            :disabled="!isNew"
            />
          
          <v-textarea label="Product Description" rows="3" outlined 
            v-model="newItem.productDesc" style="font-size: 0.9em;" 
            :disabled="newItem.productName ? false : true"
            />
        </span>

        <div style="display: flex; flex-direction: column">
          <span style="display: flex; " class="mx-1">
            <v-text-field  dense outlined v-on:keypress="$isNumber" class="mx-1" autocomplete="off"
                label="Quantity **" v-model="newItem.qnty" 
                :disabled="newItem.productName ? false : true"
                :error="formError.qnty"
                :error-messages="formError.qnty ? `Quantity cannot be 0` : ''"
                />

            <v-select :items="units" :menu-props="{offsetY: true }" dense outlined class="mx-1"
                label="Unit **" v-model="newItem.unit" 
                :disabled="newItem.productName ? false : true"
                :error="formError.unit"
                :error-messages="formError.unit ? `Unit cannot be empty` : ''"
                />

            <v-text-field dense outlined v-on:keypress="$isNumber" class="mx-1" autocomplete="off"
                label="Rate **" v-model="newItem.rate" 
                :disabled="newItem.productName ? false : true"
                />
    
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <v-text-field label="Discount" dense outlined v-on="on" autocomplete="off"
                  :append-icon="discountinpercent ? 'mdi-percent' : 'mdi-numeric'"
                  @click:append="changediscounttype"
                  v-on:keypress="$isNumber" 
                  v-model="discountValue"
                  :disabled="newItem.productName ? false : true"
                  />
              </template>
              <span v-show="!discountinpercent"> Click on <v-icon color="white">mdi-numeric</v-icon> to change to percentage</span>
              <span v-show="discountinpercent"> Click on <v-icon color="white">mdi-percent</v-icon> to change to Numeric</span>
            </v-tooltip>
          </span>

          <span style="display: flex;" class="mx-1">
            <span style="max-width: 350px">
              <v-select :items="taxes" :menu-props="{offsetY: true }" dense outlined multiple no-resize class="caption mx-1"
                  label="Tax Applicable **" v-model="newItem.taxApplicable"
                  :disabled="newItem.productName ? false : true"
                  />
            </span>
            <span style="display: flex; flex-wrap: wrap" v-if="newItem.taxValue.length > 0">      
              <span v-for="(item, index) in newItem.taxApplicable" :key="index" style="width: 138px">
                <v-text-field dense outlined append-icon="mdi-percent" v-on:keypress="$isNumber" class="mx-1 my-n2" autocomplete="off"
                    :label="item"  v-model="newItem.taxValue[getTaxIdIndex(item)].percentage"
                    @input="calculatePrice" 
                    :disabled="newItem.productName ? false : true"
                    />
              </span>
            </span>
          </span>
        </div>

        <span style="display: flex; max-width: 225px; min-width: 175px; max-height: 125px; ">
          <v-list-item two-line>
            <v-list-item-content>
              <v-list-item-title class="my-1" align="right" style="font-size: 1.2em; color:grey" >Amount</v-list-item-title>
              <v-list-item-title class="my-1" align="right" style="font-size: 1.45em; font-weight:500" > {{$formatMoney(newItem.grossAmount)}} </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </span>
      </div>
      </transition>

      <div style="display:flex; justify-content: space-between">
          <span style="display:flex; ">
          <v-chip label small color="grey" text-color="white" class="mx-4 mt-3" v-show="showitementrybar"> {{recordnumber}} Record </v-chip>
          <div class="mt-3" v-show="showitementrybar">
            <v-chip outlined label small > Net: {{$formatMoney(baseprice)}} </v-chip> - 
            <v-chip outlined label small>Discount: {{$formatMoney(isNaN(newItem.discount.numericvalue) ? 0 : newItem.discount.numericvalue)}}</v-chip> + 
            <v-chip outlined label small>Tax: {{$formatMoney(isNaN(newItem.totalTaxNumericValue) ? 0 : newItem.totalTaxNumericValue)}} </v-chip>
          </div>
          </span>
          <span v-if="isNew">
            <v-btn large color="primary" class="mx-2" v-show="showitementrybar" @click="resetItemEntry()">Reset</v-btn>
            <v-btn large color="primary" class="mx-2" v-show="showitementrybar" @click="addItem()">Add Item</v-btn>
            <v-btn :class="showitementrybar ? 'mx-2 mt-2' : 'mx-2 mt-n2'" fab dark x-small :color="showitementrybar ? 'grey' : 'green'" @click="showitementrybar = !showitementrybar"> 
                <v-icon dark v-show="showitementrybar">mdi-arrow-collapse-up</v-icon> <v-icon dark v-show="!showitementrybar">mdi-plus</v-icon> 
            </v-btn>
          </span>
          <span v-else>
            <v-btn large color="primary" class="mx-2" v-show="showitementrybar" @click="cancelediting()">Cancel</v-btn>
            <v-btn large color="primary" class="mx-2" v-show="showitementrybar" @click="addItem()">Save</v-btn>
          </span>
      </div>
    </div>
</template>

<script>
export default {
    name: 'BillingItemEntryForm',
    components: {},
    props:{
        recordnumber: Number,
        isNew: Boolean
    },
    computed:{
        products(){
            return this.$store.getters.getProductDetails;
        },
        units(){
            return this.$store.getters.getAvailableUoM;
        },
        taxes(){
            return this.$store.getters.getAvailableTaxes;
        },
        baseprice(){
            return this.newItem.qnty * this.newItem.rate;
        },
        netprice(){
            return parseFloat(this.newItem.qnty * this.newItem.rate - this.newItem.discount.numericvalue, 10);
        }
    },
    watch:{
        'product' : function(newVal){
            if(this.isNew)
            if(newVal && typeof newVal === 'object')
            {
                this.newItem.productName = newVal.name.trim();
                this.newItem.productDesc = newVal.desc;
                this.newItem.qnty = 1;
                this.newItem.unit = newVal.unit;
                this.newItem.rate = newVal.rate;
                this.discountValue = 0;
                this.newItem.isnew = false;
                this.newItem.taxApplicable = [];
                this.newItem.taxApplicable.push(...newVal.applicableTaxes);
                this.newItem.totalTaxPercentage = 0;
                this.newItem.totalTaxNumericValue = 0;
                this.formError.productname = false;
            }
            else
            {
                this.newItem.productName = newVal ? newVal.trim() : '';
                this.newItem.productDesc = '';
                this.newItem.qnty = 1;
                this.newItem.unit = '';
                this.newItem.rate = 0;
                this.newItem.isnew = true;
                this.discountValue = 0;
                this.newItem.taxApplicable = [];
                this.newItem.grossAmount = 0;
                this.newItem.totalTaxPercentage = 0;
                this.newItem.totalTaxNumericValue = 0;
                this.formError.productname = false;
            }
        },
        'newItem.taxApplicable': function(){
            this.newItem.taxValue = [];
            this.newItem.taxApplicable.forEach(x => this.newItem.taxValue.push({ taxid: x, percentage: 0, numericValue: 0 }));
        },
        'newItem.qnty': function(newVal){
            if(isNaN(newVal) || newVal == 0 )
                this.formError.qnty = true;
            else
            {
                this.formError.qnty = false;
                this.newItem.qnty = parseFloat(newVal, 10);
                this.calculatePrice();
            }
        },
        'newItem.rate': function(newVal){
            this.newItem.rate = isNaN(newVal) ? 0 : parseFloat(newVal, 10);
            this.calculatePrice();
        },
        'discountValue': function(newVal){
            this.discountValue = isNaN(newVal) ? 0 : parseFloat(newVal, 10);
            this.calcDiscount();
        }
    },
    data : () => ({
        showitementrybar: true,
        discountinpercent: true,
        isnew: false,
        discountValue: 0,
        product: null,
        newItem:{
          productName: '',
          productDesc: null,
          qnty: 1,
          unit: '',
          rate: 0,
          discount:{
              percentage: 0,
              numericvalue: 0,
            },
          taxApplicable: [],
          taxValue:[], 
          totalTaxPercentage: 0,       
          totalTaxNumericValue: 0,
          grossAmount: 0
        },
        formError:{
            productname : false,
            qnty: false,
            unit: false
        }
    }),
    methods: {
        getTaxIdIndex(index)
        {
            return this.newItem.taxValue.findIndex(x => x.taxid === index)
        },
        changediscounttype(){
            this.discountinpercent = !this.discountinpercent;
            this.calcDiscount();
        },
        calcDiscount()
        {
            if( this.discountinpercent )
            {
                this.newItem.discount.percentage = this.discountValue;
                this.newItem.discount.numericvalue = parseFloat( ((this.discountValue / 100) * this.baseprice).toFixed(3), 10);
            }
            if( !this.discountinpercent )
            {
                this.newItem.discount.numericvalue = this.discountValue;
                this.newItem.discount.percentage = parseFloat((this.discountValue / this.baseprice).toFixed(3), 10);
            }
            this.calculatePrice();
        },
        calculatePrice(){
            this.newItem.taxValue.forEach(x => x.numericValue = parseFloat((x.percentage/100) * this.netprice, 10) );
            this.newItem.totalTaxPercentage = this.newItem.taxValue.length > 0 ? this.newItem.taxValue.map(x => x.percentage == '' ? 0 : parseFloat(x.percentage, 10)).reduce((a, b) => a + b) : 0;
            this.newItem.totalTaxNumericValue = this.newItem.taxValue.length > 0 ? this.newItem.taxValue.map(x => x.numericValue).reduce((a, b) => a + b) : 0;

            this.newItem.totalTaxNumericValue = this.newItem.totalTaxNumericValue < 0 ? 0 : this.newItem.totalTaxNumericValue;
            this.newItem.grossAmount = parseFloat( this.netprice + this.newItem.totalTaxNumericValue, 10) < 0 ? 0 : parseFloat( this.netprice + this.newItem.totalTaxNumericValue, 10);
        },

        resetItemEntry()
        {
            this.discountinpercent = true;
            this.discountValue = 0;
            this.product = null;
            this.newItem = {
                productName: '',
                productDesc: null,
                qnty: 1,
                unit: '',
                rate: 0,
                discount:{
                    percentage: 0,
                    numericvalue: 0,
                  },
                taxApplicable: [],
                taxValue:[],
                totalTaxPercentage: 0,      
                totalTaxNumericValue: 0,
                grossAmount: 0
            }
            this.$refs.productselection.focus()
        },
        addItem()
        {
            if(this.newItem.productName.trim() === '' )
                this.formError.productname = true;
            else if(this.newItem.qnty === '' || this.newItem.qnty <= 0)
                this.formError.qnty = true;
            else if(this.newItem.unit.trim() === '' )
                this.formError.unit = true;
            else
            {
                this.formError.unit = false;
                this.$emit('newItem', Object.assign({discountinpercent : this.discountinpercent}, this.newItem));
                this.resetItemEntry();
            }
        },
        cancelediting()
        {
            this.resetItemEntry();
            this.$emit('cancelEdit');
        },
        editItem(itemdata)
        {
            this.product = {
                name : itemdata.productName
            };
            this.discountinpercent = itemdata.discountinpercent;
            this.discountValue = itemdata.discountinpercent ? itemdata.discount.percentage : itemdata.discount.numericvalue;

            this.newItem.productName = itemdata.productName;
            this.newItem.productDesc = itemdata.productDesc;
            this.newItem.qnty = itemdata.qnty;
            this.newItem.unit = itemdata.unit;
            this.newItem.rate = itemdata.rate;
            this.newItem.taxApplicable = itemdata.taxApplicable;
            //this.newItem.taxValue = [];
            
            this.$nextTick(() => {
                    let self= this;
                    //this.newItem.discount.percentage = itemdata.discount.percentage;
                    //this.newItem.discount.numericvalue = itemdata.discount.numericvalue;
                    let updateValue = function(index, numericValue, percentage){
                        self.newItem.taxValue[index].numericValue = numericValue;
                        self.newItem.taxValue[index].percentage = percentage;
                    };
                    itemdata.taxValue.forEach(x =>  updateValue(this.newItem.taxValue.findIndex(y => y.taxid == x.taxid), x.numericValue, x.percentage)  )
                    this.newItem.totalTaxPercentage = itemdata.totalTaxPercentage;     
                    this.newItem.totalTaxNumericValue = itemdata.totalTaxNumericValue;
                    this.newItem.grossAmount = itemdata.grossAmount;
                    })

        }

    },
}      

</script>


<style scoped>
.bounce-enter-active {
  animation: bounce-in .3s;
}
.bounce-leave-active {
  animation: bounce-in .3s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  15% {
    transform: scale(0.15);
  }
  30% {
    transform: scale(0.31);
  }
  45% {
    transform: scale(0.46);
  }
  60% {
    transform: scale(0.62);
  }
  75% {
    transform: scale(0.78);
  }
  85% {
    transform: scale(0.9);
  }
  95% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
</style>