<template>
  <v-container id="CompanyConfig" class="container--fluid">
    <div class="companyconfigheader">
        <span style="display: flex"> Company Configuration  </span>
    </div>
    
    <div class="form">

        <div class="row"> 
            <span class="row-label"><v-label> Company Name </v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-full-text"><v-text-field  dense class="textbox font-weight-medium" style="font-size: 1.25em" v-model="companyconfig.name" autocomplete=false /></span>
        </div>
        <div class="row mt-n1">
            <span class="row-label"><v-label> Company Logo </v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-small-text">
                <v-file-input color="blue-grey" dense class="mt-1 white--text" v-model="companyconfig.logo" /> 
            </span>
        </div>
        
        <div class="row"> 
            <span class="row-label"><v-label> Tax Number </v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-full-text"><v-text-field  dense class="textbox font-weight-medium" style="font-size: 1.25em" autocomplete=false v-model="companyconfig.taxnumber"/></span>
        </div>

        <div class="row"> 
            <span class="row-label"><v-label> Comany Billing Address </v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-small-text"><v-select :items="availablecountries" :menu-props="{offsetY: true }" dense class="textbox font-weight-medium" label="Country" v-model="companyconfig.country"/></span>
            <span class="row-small-text"><v-select :items="getavailableState" :menu-props="{offsetY: true }" dense class="textbox font-weight-medium"  label="State" v-model="companyconfig.state"/></span>
        </div>
        <div class="row mt-n1">
            <span class="row-label-spacer" /><span class="row-label-seperator-spacer"/>
            <span class="row-small-text"><v-text-field  dense class="textbox font-weight-medium" label="Postcode" v-model="companyconfig.postcode"/></span>
            <span class="row-small-text"><v-text-field  dense class="textbox font-weight-medium" label="City" v-model="companyconfig.city"/></span>
        </div>
        <div class="row mt-n1">
            <span class="row-label-spacer" /><span class="row-label-seperator-spacer"/>
            <span class="row-full-text"><v-text-field  dense class="textbox font-weight-medium" label="Address" v-model="companyconfig.address"/></span>
        </div>

        <div class="row"> 
            <span class="row-label"><v-label> Company Contact Details </v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-small-text"><v-text-field  dense class="textbox font-weight-medium" label="Phone Number" v-model="companyconfig.phonenumber"/></span>
            <span class="row-small-text"><v-text-field  dense class="textbox font-weight-medium" label="Email Address" v-model="companyconfig.email"/></span>
        </div>

        <div class="row"> 
            <span class="row-label"><v-label> Base Currency</v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-full-text"><v-select  dense class="textbox font-weight-medium"  label="Name - Symbol" :items="['Indian Rupee - INR(₹)']" v-model="companyconfig.basecurrency"/></span>
        </div>

        <div class="row"> 
            <span class="row-label"><v-label> Financial Period</v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-small-text">
                <v-menu v-model="fpstart" offset-y min-width="200px">
                    <template v-slot:activator="{ on }">
                    <v-text-field v-model="companyconfig.finStart" dense label="Start" class="textbox font-weight-medium" append-icon="mdi-calendar"  @click:append="fpstart = true" readonly v-on="on"></v-text-field>
                    </template>
                    <v-date-picker v-model="companyconfig.finStart" no-title @input="fpstart = false;" scrollable></v-date-picker>
                </v-menu>
            </span>
            <span class="row-small-text"><v-text-field v-model="companyconfig.finEnd" dense label="End" class="textbox font-weight-medium" readonly></v-text-field>

            </span>
        </div>

        <div class="row"> 
            <span class="row-label"><v-label> Numbering System</v-label></span>
            <span class="row-label-seperator"><v-label>:</v-label></span>
            <span class="row-small-text"><v-select  dense  class="textbox font-weight-medium" label="Decimal Seperator" :items="['DOT (.)','COMMA (,)']" :menu-props="{offsetY: true, auto: false, overflowY: true }" v-model="companyconfig.decimalSeperator" :hint="companyconfig.decimalSeperator === 'DOT (.)' ? 'Ten thousand = 10,000.00' : companyconfig.decimalSeperator === 'COMMA (,)' ? 'Ten thousand = 10.000,00' : ''" persistent-hint/></span>
            <span class="row-small-text"><v-select  dense class="textbox font-weight-medium" label="Show in Millions" :items="['YES','NO']" :menu-props="{offsetY: true }" v-model="companyconfig.showinMillions" :hint="companyconfig.showinMillions === 'YES' ? '1000000 = 1 Million' : companyconfig.showinMillions === 'NO' ? '1000000 = 10 Lakhs' : ''" persistent-hint/></span>
        </div>

        <div class="row justify-end mt-12 pb-5"> 
            <span class="row-small-text"><v-btn color="#BFBFBF" block tile class="ml-2" @click="submitnewpassword()" :disabled="isloading"> Reset  </v-btn></span>
            <span class="row-small-text"><v-btn color="success" block tile class="ml-2"> Save  </v-btn> </span>
        </div>

    </div>
    
  </v-container>
  
</template>

<script>
export default {
    name: 'CompanyConfig',
    watch:{
        "companyconfig.finStart": function(newval)
        {
            let dt = new Date(newval);
            let yr = dt.getFullYear()
            let enddt =  new Date(new Date(dt.setFullYear(yr + 1)).setDate(0));
            this.companyconfig.finEnd = enddt.getFullYear()+'-'+(enddt.getMonth() + 1).toString().padStart(2,'0')+'-'+enddt.getDate()
        }
    },
    computed:{
      availablecountries(){
            return this.$store.getters.getCountries
        },
      getavailableState(){
            return this.$store.getters.getState(this.companyconfig.country)
        }
    },
    data: () => ({
        isloading : false,
        fpstart: false,
        decimalSeperator:null,
        companyconfig:{
            name: null,
            logo: null,
            country: '',
            state: null,
            postcode: null,
            city: null,
            address: null,
            phonenumber: null,
            email: null,
            taxnumber: null,
            basecurrency: null,
            finStart: null,
            finEnd: null,
            decimalSeperator: null,
            showinMillions: null
        }
    }),
    methods: {
        submitnewpassword: function(){
            this.isloading = true;
            setTimeout(()=>{
                this.isloading = false;
            },3000)
        }
    }
    
}
</script>

<style scoped>
#CompanyConfig{
    background-color: white;
    -webkit-box-shadow: 0 0 3px #8B8B8C;
        box-shadow: 0 0 3px #8B8B8C;
    min-height: 100%;
}

.companyconfigheader{
    height: 50px;
    background-color: #0AB2BF;
    margin: -13px -13px 10px -13px;
    display: flex;
    align-items: center;
}

.companyconfigheader > span{
  font-size: 1.6em;
  font-weight: 600;
  padding-left: 30px;
  font-family: "Source Sans Pro", Roboto;
  color: rgb(255, 255, 255);
  letter-spacing: 0.03em;
}

.form{
    width: 75%;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
}

.textbox >>> input{
    margin-left: 8px;
    margin-bottom: 2px;
}

.row{
    margin-top: 30px;
}

.row .row-label, .row .row-label-spacer{
    min-width: 225px;
    margin-top:6px;
}

.row .row-label-seperator, .row .row-label-seperator-spacer {
    min-width: 30px;
    margin-top:6px;
}

.row .row-full-text{
    min-width: 71%;
    max-width: 71%;
    margin-left: 10px
}

.row .row-small-text{
    min-width: 35%;
    max-width: 35%;
    margin-left: 10px
}





@media only screen and (max-width: 1275px) {

    .row{
        display: flex;
        flex-direction: column;
    }

    .row .row-label-seperator, .row .row-label-seperator-spacer{
        display: none;
    }

    .row .row-label{
        margin-bottom: 8px;
    }

    .row .row-small-text, .row .row-full-text{
        min-width: 100%;
        max-width: 100%;
    }
}

@media only screen and (max-width: 500px) {
    .templateform > .banner > .companyname
    {
        padding-top: 20px;
        font-size: 2.5em;
    }

    .templateform > .banner > .companytagname
    {
        font-size: 1em;
    }
    .templateform > .content > .tabs{
        flex-direction: column;
    }
   
}

@media only screen and (max-width: 320px) {
    .templateform > .banner > .companyname
    {
        padding-top: 40px;
        font-size: 1.5em;
    }
}

</style>