<template>
<v-app style="background-color: #6F757F">
    <v-container  class="fill-height justify-center " fluid >
        <v-card width="1050px" class="vcard">

            <div class="companyinfo">
                <div class="display-2 companyname" > One ERP </div>
                <div class="headline font-weight-light companymotive" v-show="!showcontactform && !showcontactresponse"> 
                    Experiance the most efficient way of handling your accounts.
                </div>
                <div class="title font-weight-regular comapnytagline mb-12" v-show="!showcontactform && !showcontactresponse"> Process Simplified. </div>
                
                <div v-show="showcontactform" class="contactform">
                    <v-text-field dense label="Full Name" class="contactform mt-8" outlined :disabled="isloading" v-model="contactform.fullname" ref="contactformfulname" v-on:keyup="$event.keyCode === 13 ? $refs.contactformemail.focus() : ''" />
                    <v-row class="mx-1">
                        <v-text-field dense label="Email Address" class="contactform " outlined :disabled="isloading" v-model="contactform.email" ref="contactformemail" v-on:keyup="$event.keyCode === 13 ? $refs.contactformcontact.focus() : ''" />
                        <v-text-field dense label="Contact Number" class="contactform ml-1" outlined :disabled="isloading" v-model="contactform.contactnumber" ref="contactformcontact" v-on:keyup="$event.keyCode === 13 ? $refs.contactformquery.focus() : ''" />
                    </v-row>
                    <v-textarea label="How can we help you today ?" class="contactform" outlined :disabled="isloading" v-model="contactform.query" no-resize ref="contactformquery" v-on:keyup="$event.keyCode === 13 ? $refs.contactformfulname.focus() : ''" />
                    <v-row class="justify-end mx-2">
                        <v-btn tile small color="#CCD9D7" class="mr-3 mb-8 mt-n4" @click="showcontactform = false"> Cancel </v-btn>
                        <v-btn tile small color="#CCD9D7" class="mb-8 mt-n4" @click="submitcontactform()"> Submit </v-btn>
                    </v-row>
                </div>

                <div class="headline font-weight-light companymotive mb-12" v-show="!showcontactform && showcontactresponse"> 
                    Thank you for your query, we will get back to you at the earliest.
                </div>


                <div class="companyinfobtngrp" >
                <v-row >
                    <v-btn tile outlined small color="#CCD9D7" class="mx-2 my-1"> Home Page </v-btn>
                    <v-btn tile outlined small color="#CCD9D7" class="mx-2 my-1"> Imprint </v-btn>
                    <v-btn tile :outlined="!showcontactform" small color="#CCD9D7" class="mx-2 my-1" @click="showcontactform = true" > Contact us </v-btn>
                    <v-btn tile :outlined="!signup" small color="#CCD9D7" class="mx-2 my-1" @click="signup = true" > Sign Up </v-btn>
                </v-row>
                </div>
            </div>

        
            <div class="registrationform">
                
                <div class="regformcompanyname"> One ERP </div>
            <!-- Sign In Form -->
                <div class="mx-6 px-5 mt-6" v-show="!resetpassword && !signup">
                    <div class="headline font-weight-medium regformsignintext mb-6" > Sign in </div>
                    <div style=" display: flex; color: #D10D1F; border-radius: 2px; border: 1px solid #D10D1F; margin-bottom: 24px" v-show="invalidcred">
                        <ul style="list-style-type:none; padding: 0px; padding: 5px 10px; max-width: 300px;">
                            <li class="subtitle-1 font-weight-regular">Invalid crediantials..! </li>
                            <li style=" font-size:0.75em; overflow-wrap: break-word; max-width: 300px"><i>You can recover password by clicking on "Forgot my password" or contact your Administrator</i></li>
                        </ul>
                    </div>
                    <div style=" display: flex; color: #D10D1F; border-radius: 2px; border: 1px solid #D10D1F; margin-bottom: 24px" v-show="invaliduser">
                        <ul style="list-style-type:none; padding: 0px; padding: 5px 10px; max-width: 300px;">
                            <li style=" font-size:0.75em; overflow-wrap: break-word; max-width: 300px"> Sorry, We cannot find you in our system. Please sign up or contact Administrator for access.</li>
                        </ul>
                    </div>
                    <v-text-field flat
                    label="Username or Login ID"  
                    prepend-icon="mdi-account-box" 
                    class="logintext font-weight-medium mt-2" 
                    style="letter-spacing: 0.08em"
                    v-on:keyup="checkPassword"
                    v-model="usernamevalue"
                    type="email"
                    ref="username"
                    :disabled="isloading"
                    ></v-text-field>

                    <v-text-field flat
                    label="Password"  
                    prepend-icon="mdi-lock"  
                    style="letter-spacing: 0.08em"
                    class="logintext font-weight-medium mb-5" :append-icon="value ? 'mdi-eye-off' : 'mdi-eye'" 
                    @click:append="() => (value = !value)" :type="value ? 'password' : 'text'" 
                    v-on:keyup="authenticateonenter"
                    v-model="passwordvalue"
                    ref="password"
                    :disabled="isloading"></v-text-field>
                    
                    <v-progress-linear class="ml-4" indeterminate color="green darken-2" v-show="isloading"/>
                    <v-btn color="success" tile block class="ml-4" @click="authenticate()" :disabled="isloading"> 
                    <span v-show="!isloading"> Login </span> <span v-show="isloading"> Authenticating </span>
                    </v-btn>
                    <v-progress-linear class="ml-4" indeterminate color="green darken-2" v-show="isloading"/>


                    <div class="caption mt-2 forgetpassword" @click="resetpassword = true" :disabled="isloading"> Forgot my password</div>
                    
                    <div style="margin: 0px 15px 10px 50px; text-align: center; border-bottom: 1px solid #000; line-height: 0.03em;" :class="`overline ${ invaliduser || invalidcred ? 'mt-8' : 'mt-12'}`" > <span style="background:#F2F2F2; padding:0 10px; ">OR</span> </div> 
                    <v-btn color="#E84703" block text :class=" invalidcred || invaliduser ? 'mt-6 pb-6 startfreeaccbtn' : 'mt-12 startfreeaccbtn' " @click="signup = true"> Start with a free account </v-btn>
                </div>

            <!-- Reset Password Form -->
                <div class="mx-6 px-5 mt-6" v-show="resetpassword && !securitycodesent">
                    <div class="headline font-weight-medium regformsignintext mb-6" > Forget your password ? </div>
                    <div class="body-2 mb-6" style="overflow-wrap: break-word; max-width: 300px"> let us know your email or login id, we will help you to recover your account.</div>
                    <v-text-field flat
                    label="Username or Login ID"  
                    prepend-icon="mdi-account-box" 
                    class="logintext font-weight-medium mt-2 mb-6" 
                    style="letter-spacing: 0.08em"
                    v-on:keyup="checkPassword"
                    v-model="usernamevalue"
                    ref="username"
                    :disabled="isloading"></v-text-field>

                    <div style="display: flex; justify-content: center;  margin-left: 20px;" v-show="recoverpassword">
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    </div>

                    <v-progress-linear class="ml-4" indeterminate color="green darken-2" v-show="isloading"/>
                    <v-btn color="success" tile block class="ml-4" @click="askforpasswordreset()" :disabled="isloading" v-show="recoverpassword"> 
                        <span v-show="!isloading"> Recover Password </span> <span v-show="isloading"> PROCESSING </span>
                    </v-btn>
                    <v-btn color="success" tile block class="ml-4" @click="forgotpassword()" :disabled="isloading" v-show="!recoverpassword"> 
                        <span v-show="!isloading"> Reset Password </span> <span v-show="isloading"> PROCESSING </span>
                    </v-btn>
                    <v-progress-linear class="ml-4" indeterminate color="green darken-2" v-show="isloading"/>
                    
                    <div class="caption mt-8 forgetpassword" v-show="!recoverpassword" @click="recoverpassword = true" :disabled="isloading"> I got a Security Code.</div>
                    <div class="caption mt-8 forgetpassword" v-show="recoverpassword" @click="recoverpassword = false" :disabled="isloading"> No, I dont have Security Code</div>
                    <div class="caption mt-4 forgetpassword" @click="resetpassword = false; signup = false" :disabled="isloading"> Back to Log in </div>
                </div>
            
            
            <!-- Reset Form Response -->
                <div class="mx-6 px-5 mt-6" v-show="resetpassword && securitycodesent">
                    <div class="headline font-weight-medium regformsignintext mb-6" > Now Relax, We got you..! </div>

                    <div class="body-2 mb-4" style="overflow-wrap: break-word; max-width: 312px" v-show="securitycodesent === 'email'"> 
                        We have sent you the <b>Security Code</b> and instructions over your email to recover your account.
                    </div>

                    <div class="body-2 mb-4" style="overflow-wrap: break-word; max-width: 312px"> 
                        We have informed your Administrator to recover your account. Please contact yor Administrator for your Security Code
                    </div>
                    
                    <div class="body-2 mb-8" style="overflow-wrap: break-word; max-width: 300px"> 
                        If you already have the security code, Enter here.
                    </div>

                    <div style="display: flex; justify-content: center" class="mb-2">
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    </div>
                    
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>
                    <v-btn color="success" tile block class="ml-2" @click="askforpasswordreset()" :disabled="isloading"> 
                    <span v-show="!isloading"> Recover Password </span> <span v-show="isloading"> PROCESSING </span>
                    </v-btn>
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>
                </div>

            <!-- Signup Form Response -->
                <div class="mx-6 px-5 mt-6" v-show="signup && !signupinfosent">
                    <div class="headline font-weight-medium regformsignintext mb-6" > Sign Up </div>
                    
                    <v-text-field 
                        flat label="Full Name" class="logintext font-weight-medium mt-2" style="letter-spacing: 0.08em" 
                        v-model="signupdetails.fullname" ref="fullname" :disabled="isloading" v-on:keyup="$event.keyCode === 13 ? $refs.emailaddr.focus() : ''" />

                    <v-text-field 
                        flat label="Email Address" class="logintext font-weight-medium" style="letter-spacing: 0.08em" 
                        v-model="signupdetails.email" ref="emailaddr" :disabled="isloading" v-on:keyup="$event.keyCode === 13 ? $refs.phonecountrycode.focus() : ''" />
                    
                    <v-row class="pl-1 pr-1"> 
                        <v-select 
                            style="max-width:60px" :items="countrycode" :menu-props="{offsetY: true }" class="subtitle-1 font-weight-bold" 
                            v-model="signupdetails.countrycode" ref="phonecountrycode" :disabled="isloading" v-on:keyup="$event.keyCode === 13 ? $refs.mobilenumber.focus() : ''" />
                        <v-text-field 
                            flat label="Mobile Number" class="logintext font-weight-medium pl-1" style="letter-spacing: 0.08em" 
                            v-model="signupdetails.mobilenumber" ref="mobilenumber" :disabled="isloading"  v-on:keyup="$event.keyCode === 13 ? $refs.postcode.focus() : ''" />
                    </v-row>
                    
                    <v-row class="pl-1 pr-1"> 
                        <v-text-field 
                            flat label="Postal Code" class="font-weight-medium pl-1 body-1" style=" max-width:140px"
                            v-model="signupdetails.postcode" ref="postcode" :disabled="isloading"  v-on:keyup="$event.keyCode === 13 ? $refs.country.focus() : ''" />
                        <v-text-field 
                            flat label="Country" class="font-weight-medium pl-1 body-1" style="letter-spacing: 0.08em; max-width:180px" 
                            v-model="signupdetails.country" ref="country" :disabled="isloading"  v-on:keyup="$event.keyCode === 13 ? $refs.terms.focus() : ''" />
                    </v-row>
                    
                    <v-row class="ml-1 mr-1 mb-6 mt-1">
                        <v-checkbox v-model="signupdetails.termsreadandagreed"  color="success"  value="indigo"  hide-details class="mt-n2" ref="terms" :disabled="isloading"/>
                        <span class="subtitle-2"> I agree for <a> terms and conditions </a> </span>
                    </v-row>
                    
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>
                    <v-btn color="success" tile block class="ml-2" @click="registernewuser()" :disabled="isloading"> 
                    <span v-show="!isloading"> Sign Up </span> <span v-show="isloading"> PROCESSING </span>
                    </v-btn>
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>

                    <div class="caption mt-4 forgetpassword" @click="resetpassword = false;  signup = false" :disabled="isloading"> Back to Log in </div>
                </div>    
            
                <!-- signup Form Response -->
                <div class="mx-6 px-5 mt-6" v-show="signup && signupinfosent">
                    <div class="headline font-weight-medium regformsignintext mb-6" > Thanks you for Signinig Up </div>

                    <div class="body-2 mb-4" style="overflow-wrap: break-word; max-width: 312px" > 
                        We are almost there.! We have sent you further instructions over the mail. Please click on the link or enter the Security Code that you have received over your email.
                    </div>


                    <div style="display: flex; justify-content: center" class="mb-2 mt-8">
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    <v-text-field outlined dense class="headline font-weight-bold ml-2 securitycode" maxlength="3" style="max-width: 85px;" :disabled="isloading"/>
                    </div>
                    
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>
                    <v-btn color="success" tile block class="ml-2" @click="askforpasswordreset()" :disabled="isloading"> 
                    <span v-show="!isloading"> Complete registraion </span> <span v-show="isloading"> PROCESSING </span>
                    </v-btn>
                    <v-progress-linear class="ml-2" indeterminate color="green darken-2" v-show="isloading"/>
                </div>

          </div>

        
            
        </v-card>
    </v-container>
</v-app>
</template>
<script>
  export default {
    name: 'Login',

    data: () => ({
      value: true,
      usernamevalue: null,
      passwordvalue: null,
      invalidcred: false,
      invaliduser: false,
      resetpassword: false,
      recoverpassword: false,
      showcontactform: false,
      showcontactresponse: false,
      securitycodesent: false,
      signupinfosent: false,
      isloading: false,
      signup: false,
      countrycode: ['+91'],
      signupdetails:{
          fullname: null,
          email: null,
          countrycode : '+91',
          mobilenumber: null,
          postcode: null,
          country: 'INDIA',
          termsreadandagreed: false
      },
      contactform:{
          fullname: null,
          email: null,
          contactnumber: null,
          query: null
      }
    }),
    methods : {
        checkPassword: function(e)
        {
            if (e.keyCode === 13)
                this.$refs.password.focus()
            this.usernamevalue = this.usernamevalue.toLowerCase();
        },
        authenticateonenter: function(e)
        {
            if(e.keyCode === 13 && (this.usernamevalue === null || this.usernamevalue === undefined || this.usernamevalue.trim() === '' ))
            {
                this.$refs.username.focus();
            }
            else if(e.keyCode === 13 && (this.passwordvalue === null || this.passwordvalue === undefined || this.passwordvalue.trim() === '' ))
            {
                this.$refs.password.focus();
            }
            else if(e.keyCode === 13 && this.usernamevalue.length > 1 && this.passwordvalue.length > 1)
                this.authenticate()
        },
        authenticate : function(){
            this.isloading = true;
            setTimeout(() => {
                if(this.usernamevalue === 'admin' && this.passwordvalue === 'admin')
                    this.$router.push('/dashboard')
                else
                    this.invalidcred= true;
                this.isloading = false;
            }, 5000)
            
        },
        forgotpassword()
        {
            this.isloading = true;
            setTimeout(()=>{
                this.isloading = false;
                this.securitycodesent = true;
            }, 10000)
            
        },
        askforpasswordreset()
        {
            this.isloading = true;
            setTimeout(()=>{
                this.isloading = false;
                this.resetpassword = false; 
                this.securitycodesent = false; 
                this.signupinfosent = false;
                this.signup = false;
            }, 10000)
            
        },
        registernewuser()
        {
            this.isloading = true;
            setTimeout(()=>{
                this.isloading = false;
                this.signupinfosent = true; 
            }, 10000)
        },
        submitcontactform()
        {
            this.showcontactform = false;
            this.showcontactresponse = true;
            setTimeout(()=>{
                this.showcontactform = false;
                this.showcontactresponse = false;
            }, 5000)
        }
    }
  }
</script>

<style scoped>

.contactform{
    margin-left: 20px;
    margin-right: 20px;
}

.contactform >>> label, .contactform >>> input, .contactform >>> textarea{
    color:#CCD9D7;
}

.contactform >>> fieldset{
    border: 1px solid #CCD9D7;
}

.link:hover{
    cursor: pointer;
    text-decoration: underline;
    font-weight: bold;
    color: #0439D9;
}

.logintext{
    font-size: 1.2em;
    font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.forgetpassword{
    text-align: center; 
    margin-left: 40px; 
    color:blue
}

.forgetpassword:hover{
    font-weight: bold;
    cursor: pointer;
}


.forgetpassword[disabled]{
    color: #636E72;
    pointer-events:none;
}

.companyinfo {
    min-width:60%; 
    height: 100%; 
    float: left; 
    border-top-right-radius: 0px; 
    border-bottom-right-radius: 0px; 
    background-color: #222D32;
    color:#CCD9D7;
}

.companyinfo > .companyname{ 
    margin-top: 80px; 
    margin-left: 100px;
}

.companyinfo > .companymotive{ 
    margin-top: 40px; 
    max-width: 250px; 
    margin-left: 150px
}

.companyinfo > .comapnytagline{ 
    margin-top: 20px; 
    margin-left: 150px
}

.companyinfo > .companyinfobtngrp{ 
    margin-left: 100px; 
    margin-top: 10px
}

.registrationform{
    min-width:40%; 
    height: 100%; 
    float: right; 
    background-color: #F2F2F2;
}

.registrationform > .regformcompanyname{
    margin: 10px 100px; 
    color: #222D32;
    text-align: center;
    justify-content: center;
    display: flex;
    font-size: 3em;
    opacity: 0;
}

.registrationform > .regformsignintext{ 
    color: #222D32;
}

.startfreeaccbtn{
    margin-left: 22px;
}

.securitycode >>> input{
    text-align: center;
}

.vcard{
    height: 600px
}

@media only screen and (max-width: 999px) {
  .vcard {
    display: flex;
    flex-direction: column;
    height: 1200px
  }

  .registrationform > .regformcompanyname{
      opacity: 1;
  }

  .companyinfo, .registrationform{
      padding-bottom: 40px;
  }
}

@media only screen and (max-width: 499px) {
     .vcard {
        display: flex;
        flex-direction: column;
        height: 1400px
    }

    .contactform{
        margin-left: 5px;
        margin-right: 5px;
    }

    .companyinfo > .companyname{ 
      margin-left: 50px;
    }

    .companyinfo > .companymotive, .companyinfo > .comapnytagline{ 
      margin-left: 90px;
    }

    .companyinfo > .companyinfobtngrp{
        margin-left: 20px; 
    }

    .registrationform > .regformcompanyname{
        margin: 30px 30px; 
    }

}

@media only screen and (max-width: 380px) {

    .vcard {
        display: flex;
        flex-direction: column;
        height: 1400px
    }

    .contactform{
        margin-left: 5px;
        margin-right: 5px;
    }

    .companyinfo > .companyname{ 
      margin-left: 40px;
    }

    .companyinfo > .companymotive, .companyinfo > .comapnytagline{ 
      margin-left: 70px;
    }

    .companyinfo > .companyinfobtngrp{
        margin-left: 20px; 
    }

    .registrationform > .regformcompanyname{
      margin-top: 0px;
      padding-top: 15px;
    }

}

</style>
