<template>
  <v-row justify="center" >
    <v-dialog v-model="displayDialog" max-width="850" >
      <v-card shaped>
        <v-card-title style="font-size: 1.2em; background-color: #0AB2BF; color: white" color="red" >Select Contact Details</v-card-title>
        <v-divider />
        
        <v-layout justify-start align-center class="mt-10"> 
          <div class="headline pl-12"> {{partnerName}} </div>
        </v-layout>
        <v-layout justify-center align-center class="mt-1">           
        <span class="contentlist" style="width: 85%; display: flex;  flex-wrap: wrap; justify-content: space-between;">
        <span v-for="(item, index) in contactdetails" :key="index">
          <v-card outlined height="125px" width="350px" class="mt-2">
            <v-list>
             <div class="mx-2 mb-1" style="font-size: 0.9em; color:grey"> Contact Details <v-btn x-small color="info" class="float-right" @click="selectcontact(index)">Select</v-btn></div>
            <v-divider></v-divider>
            <v-list-item three-line class="mb-n2 mt-n1">
              <v-list-item-content>
                  <v-list-item-title class="font-weight-bold" style="font-size:0.95em"><v-icon small>mdi-account</v-icon> {{item.name}} </v-list-item-title>
                  <v-list-item-subtitle class="my-1"> <v-icon small>mdi-phone</v-icon> <span style="font-size:0.95em"> {{item.phone}} </span></v-list-item-subtitle>
                  <v-list-item-subtitle> <span style="display: inline-flex;"><v-icon small class="mr-2">mdi-email</v-icon>  <span style="font-size:0.95em"> {{item.email}} </span></span> </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            </v-list>
          </v-card> 
        </span>            
        </span>
        </v-layout>

        <v-layout justify-center align-center class="pt-4 mb-n3 mx-12" v-show="newcontactentry">  
          <v-text-field ref="name"  @keyup.enter="savecontact" :error="validate && newcontact.name.trim() === ''" dense outlined label="Name" class="mx-1" v-model="newcontact.name"/>
          <v-text-field ref="phone" @keyup.enter="savecontact" :error="validate && newcontact.phone.trim() === ''" dense outlined label="Phone Number" class="mx-1" v-model="newcontact.phone"/>
          <v-text-field ref="email" @keyup.enter="savecontact" :error="validate && newcontact.email.trim() === ''" dense outlined label="Email" class="mx-1" v-model="newcontact.email"/>
        </v-layout>
        <v-layout justify-center align-center class="mt-n5 pb-12 mx-12" v-show="newcontactentry">  
          <v-btn tile color="success" block @click="savecontact" > Save Details </v-btn>
        </v-layout>

        <v-layout justify-center align-center class="pt-4 pb-12 mx-12" v-show="!newcontactentry">  
              <v-btn tile color="success" block @click="newcontactentry = true" >Add New Contact Details</v-btn>
        </v-layout>

      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    data () {
      return {
        displayDialog: false,
        contactdetails: [],
        validate: false,
        partnerName: null,
        newcontactentry: false,
        newcontact:{
          name: '',
          phone: '',
          email: '',
        }
      }},
      methods: {
        show(contactdetails, partnerName){
          if(contactdetails || partnerName)
          {
            this.displayDialog = true;
            this.partnerName = partnerName;
            this.contactdetails = contactdetails;
          }
        },
        close(){
          this.displayDialog = false;
        },
        selectcontact(index){
          this.$emit('onClose', this.contactdetails[index]);
          this.newcontact = {
              name: '',
              phone: '',
              email: '',
            };
            this.validate = false;
            this.newcontactentry = false;
          this.close();
        },
        savecontact()
        {
          this.validate = true;
          if(this.newcontact.name.trim() !== '' && this.newcontact.phone.trim() !== '' && this.newcontact.email.trim() !== '')
          {
            this.contactdetails.push(this.newcontact);
            this.newcontact = {
              name: '',
              phone: '',
              email: '',
            };
            this.validate = false;
            this.newcontactentry = false;
          }    

        }
    }
  }
</script>

<style scoped>

span{
  user-select:none;
}

.contentlist{
  max-height: 320px;
  overflow-y: scroll;
}

.contentlist::-webkit-scrollbar {
  width: 3px;
}
 
.contentlist::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
}
 
.contentlist::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

</style>